<template>
    <div>
        <div class="wrapper">
            <div class="line"></div>
            <div class="type">
                <div class="title">排序：</div>
                <div class="values">
                    <ul>
                        <li class="clearfix"
                            :class="newest ? 'select-item ': 'item'"
                            @click="select('0')">最新</li>
                        <li class="clearfix"
                            :class="hottest ? 'select-item' : 'item'"
                            @click="select('1')">最热</li>
                    </ul>
                </div>
            </div>
            <div class="line"></div>
            <div class="cause-wrapper">
                <ul>
                    <li class="cause" v-for="(item, index) in result">
                        <div class="title">
                            <div class="name">{{item.user_name}}</div>
                            <div class="time">{{item.create_time}}</div>
                        </div>
                        <div class="desc">
                            {{item.notice_content}}
                        </div>
                        <div class="images">
                            <ul>
                                <li class="image clearfix" v-for="iv in item.notice_url">
                                    <img :src="api+iv" @click="preview(api+iv, index)">
                                </li>

                            </ul>
                            <div class="clear"></div>

                            <!--<div v-show="isShow[index]" class="preview-wrapper" >-->
                                <!--<img :src="iv">-->
                            <!--</div>-->
                            <bigimg v-show="isShow[index]"  @clickit="canclePreview(index)" :imgSrc="iv" ></bigimg>
                        </div>
                        <div class="set-video" v-if="item.notice_video !== '' ">
                            <iframe :src="item.notice_video"></iframe>
                        </div>
                        <div v-else></div>

                        <div class="clear"></div>
                        <div class="line"></div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
    import BigImg from '../public/bigimg.vue'
    import {Toast} from 'mint-ui'
    export default {
        data () {
            return {
                api: '',
                id: '',
                result: [],
                newest: true,
                hottest: false,
                iv: '',
                isShow: []
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.api = this.$store.state.api
            this.id = this.$store.state.id
            this.getboard()
        },
        components: {
            'bigimg': BigImg
        },
        methods: {
            canclePreview (i) {
                this.$set(this.isShow, i, false)
            },
            preview (url, index) {
                this.iv = url
                for (var i = 0; i < this.result.length; i++) {
                    if (index === i) {
                        this.$set(this.isShow, index, true)
                    } else {
                        this.$set(this.isShow, i, false)
                    }
                }
            },
            getboard () {
                this.$http.post(this.api + '/index/user/notice', {fid: this.id}).then(function (response) {
                    this.isShow = []
                    if (this.newest === true) {
                        this.result = response.body.data
                    } else {
                        this.result = response.body.hdata
                    }
                    if (typeof (this.result) !== 'undefined' || this.result !== []) {
                    } else {
                        for (var i = 0; i < this.result.length; i++) {
                            this.$set(this.isShow, i, false)
                        }
                    }
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 3000
                    })
                })
            },
            select (tag) {
                if (tag === '0') {
                    this.newest = true
                    this.hottest = false
                } else {
                    this.newest = false
                    this.hottest = true
                }
                this.getboard()
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     width 7.5rem
     margin-top 1.79rem
     padding-bottom 0.98rem
     background-color #ffffff
     .line
      width 7.5rem
      background-color #f9f9f9
      height 0.09rem
     .type
      width 7.5rem
      font-size 0
      display flex
      margin-top 0.24rem
      margin-bottom 0.23rem
      .title
       color #333333
       margin-left 0.3rem
       white-space nowrap
       set-size(16px)
       font-weight 500
       align-self center
      .values
       align-self center
       .item,.select-item
        color #333333
        set-size(15px)
        margin-left 0.44rem
        float left
       .select-item
        color #cbac86
       .clearfix:after
        content ' '
        display inline-block
        height 0
        line-height 0
        clear both
     .cause-wrapper
      width 7.5rem
      .cause
       width 7.5rem
       font-size 0
       .title
        display flex
        justify-content space-between
        .name
         color #CBAC86
         set-size(14px)
         margin-top 0.22rem
         margin-left 0.31rem
        .time
         color #CBAC86
         set-size(10px)
         margin-top 0.28rem
         margin-right 0.32rem
       .desc
        color #666666
        set-size(13px)
        margin 0.14rem 0.32rem 0 0.31rem
       .set-video
        width 6.84rem
        margin 0 auto
        height 3.75rem
        margin-top 0.17rem
        iframe
         width 100% !important
         height 3.75rem !important
         overflow hidden
       .images
        width 6.76rem
        margin 0 auto
        margin-top 0.29rem
        .preview-wrapper
         margin-top 0.12rem
         border solid 0.01rem #CBAC86
         width 2.61rem
         height 2.11rem
         background-size 2.61rem 2.11rem
         img
          width 2.61rem
          height 2.11rem
          background-repeat no-repeat
          background-size 2.61rem 2.11rem
        .image
         img
          width 1.14rem
          height 0.9rem
          background-size 1.14rem 0.9rem
          background-repeat no-repeat
          margin-right 0.31rem
          float left
         .select-img
          border solid 0.01rem #CBAC86
        .clearfix:after
         content '.'
         display inline-block
         height 0
         line-height 0
         clear both
       .line
        width 6.91rem
        height 0.02rem
        background-image url("line.png")
        background-size 6.91rem 0.02rem
        background-repeat no-repeat
        margin 0.18rem auto
       .clear
        clear both
</style>



