<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div class="title">我的收藏</div>
            </div>
            <div class="content">
                <ul>
                    <li class="item" v-for="item in result">
                        <div class="title">
                            <div class="name">{{item.company_name}}</div>
                            <div class="time">{{item.create_time}}</div>
                        </div>
                        <div class="step">
                            <span class="cancel" @click="cancel(item.id)"></span>
                            <span class="check" @click="tobusiness(item.id)"></span>
                        </div>
                        <div class="line"></div>
                    </li>
                </ul>
            </div>
        </div>
        <navbar selectShow="personal"></navbar>
    </div>
</template>
<script>
    import NavBar from '../navbar/navbar.vue'
    import {Toast, Indicator} from 'mint-ui'
    export default {
        data () {
            return {
                api: '',
                user_id: '',
                result: []
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.api = this.$store.state.api
            this.user_id = this.$store.state.user_id
            Indicator.open()
            this.getcollect()
        },
        components: {
            navbar: NavBar
        },
        methods: {
            tobusiness (id) {
//                企业详情
                this.$router.push({path: '/business-index', query: {business_id: id}})
            },
            getcollect () {
                this.$http.post(this.api + '/index/my/collection', {
                    user_id: this.user_id
                }).then(function (response) {
                    this.result = response.body
                    Indicator.close()
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                    Indicator.close()
                })
            },
            cancel (id) {
//                取消收藏
                this.$http.post(this.api + '/index/my/cancel', {
                    user_id: this.user_id,
                    id: id
                }).then(function (response) {
                    if (response.body.status === 200) {
                        Toast({
                            message: '成功取消',
                            position: 'bottom',
                            duration: 1000
                        })
                        this.getcollect()
                    } else {
                        Toast({
                            message: '取消失败',
                            position: 'bottom',
                            duration: 1000
                        })
                    }
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                })
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     width 7.5rem
     position absolute
     bottom 1.03rem
     top 0
     overflow-y scroll
     -webkit-overflow-scrolling touch
     .head
      width 7.5rem
      height 0.89rem
      background-color #141014
      display flex
      justify-content center
      .title
       align-self center
       color #CBAC86
       set-size(18px)
     .content
      width 7.5rem
      margin-top 0.26rem
      .item
       width 7.5rem
       .title
        display flex
        justify-content space-between
        font-size 0
        .name
         set-size(14px)
         color #333333
         margin-top 0.3rem
         margin-left 0.3rem
         margin-right 0.3rem
         align-self center
        .time
         align-self center
         set-size(14px)
         color #999999
         margin-top 0.32rem
         margin-right 0.3rem
         white-space nowrap
       .step
        display flex
        flex-direction row-reverse
        .cancel,.check
         width 1.41rem
         height 0.53rem
         background-repeat no-repeat
         background-size 1.41rem 0.53rem
         margin-top 0.32rem
        .check
         background-image url("btn_chakanqiye.png")
        .cancel
         background-image url("btn_quxiaoshoucang.png")
         margin-left 0.3rem
         margin-right 0.3rem
       .line
        width 6.91rem
        height 0.02rem
        background-image url("line.png")
        margin 0.29rem auto
</style>



