<template>
    <div>
        <div class="wrapper">
            <div class="line"></div>
            <div class="content">
                <ul>
                    <li class="item" v-for="item in result">
                        <div class="title">
                            <div class="name">
                                用户名：{{item.user_name}}
                            </div>
                            <div class="time">
                                {{item.create_time}}
                            </div>
                        </div>
                        <div class="desc">
                            {{item.evaluate_content}}
                        </div>
                        <div class="line-new"></div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
    import {Toast, Indicator} from 'mint-ui'
    export default {
        data () {
            return {
                api: '',
                user_id: '',
                result: []
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.api = this.$store.state.api
            this.user_id = this.$store.state.user_id
            Indicator.open()
            this.getreceive()
        },
        components: {},
        methods: {
            getreceive () {
                this.$http.post(this.api + '/index/my/get', {
                    user_id: this.user_id
                }).then(function (response) {
                    this.result = response.body
                    Indicator.close()
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 3000
                    })
                    Indicator.close()
                })
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../../common/stylus/base.styl"
    .wrapper
     width 7.5rem
     .line
      width 7.5rem
      background-color #f9f9f9
      height 0.05rem
     .content
      width 7.5rem
      margin-top 0.18rem
      .item
       width 7.5rem
       font-size 0
       .title
        display flex
        margin-top 0.3rem
        justify-content space-between
        .name
         set-size(14px)
         color #CBAC86
         margin-left 0.3rem
        .time
         color #CBAC86
         set-size(10px)
         margin-right 0.35rem
       .desc
        color #666666
        set-size(13px)
        margin 0.14rem 0.44rem 0 0.3rem
       .line-new
        width 6.91rem
        height 0.02rem
        background-image url("line.png")
        margin 0.22rem auto
</style>



