<template>
    <div class="nav">
        <router-link :to="{path: '/home-index'}" class="home">
            <div>
                <span v-if="selectShow === 'home'" class="home-select"></span>
                <span v-else class="home-icon"></span>
            </div>
        </router-link>
        <router-link :to="{path: '/company-index'}" replace class="company">
        <div>
            <span v-if="selectShow === 'company'" class="company-select"></span>
            <span v-else class="company-icon"></span>
        </div>
        </router-link>

        <div @click="hasLogin" class="personal">
            <div>
                <span v-if="selectShow === 'personal'" class="personal-select"></span>
                <span v-else class="personal-icon"></span>
            </div>
        </div>

    </div>
</template>
<script>
    export default {
        props: ['selectShow'],
        data () {
            return {
                user_id: ''
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
        },
        components: {},
        methods: {
            hasLogin () {
                this.user_id = this.$store.state.user_id
                if (this.user_id === 0 ||
                        typeof (this.user_id) === 'undefined' ||
                        this.user_id === '') {
                    this.$router.push({path: '/login-index'})
                } else {
                    this.$router.replace({path: '/personal-index'})
                }
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus">
    @import "../../common/stylus/base.styl"
    .nav
     width 7.5rem
     height 0.98rem
     background-color #ffffff
     position fixed
     display flex
     left 0
     bottom 0
     box-shadow 0 0 0 0.05rem #ececec
     .home
      flex 1
      .home-select,.home-icon
       display inline-block
       width 2.5rem
       height 0.98rem
       background-repeat no-repeat
       background-size 2.5rem 0.98rem
      .home-select
       background-image url("icon_shouye01.png")
      .home-icon
       background-image url("icon_shouye02.png")
     .company
      flex 1
      .company-select,.company-icon
       display inline-block
       width 2.5rem
       height 0.98rem
       background-repeat no-repeat
       background-size 2.5rem 0.98rem
     .company-select
      background-image url("icon_qiyehebanzu01.png")
     .company-icon
      background-image url("icon_qiyehebanzu02.png")
     .personal
      flex 1
      .personal-select,.personal-icon
       display inline-block
       width 2.5rem
       height 0.98rem
       background-repeat no-repeat
       background-size 2.5rem 0.98rem
      .personal-select
       background-image url("icon_gerenzhongxin01.png")
      .personal-icon
       background-image url("icon_gerenzhongxin02.png")
</style>



