<template>
    <div>
        <div class="wrapper">
            <div class="line"></div>
            <div class="type">
                <div class="desc">类型</div>
                <div class="values">
                    <ul>
                        <li class="clearfix"
                            v-for="(item, index) in category"
                            @click="getbusiness(item.category_id, index)"
                            :class="tags[index] ? 'select-value' : 'value'">
                            {{item.c_name}}
                        </li>
                        <li class="clearfix"
                            :class="limit_show ? 'select-limit' : 'limit'"
                            @click="getbusiness('', '')">不限</li>
                    </ul>
                </div>
            </div>
            <div class="line"></div>
            <div class="lists-wrapper">
                <ul>
                    <li v-for="item in result">
                        <div class="item-business">
                            <div class="image">
                                <img :src="api+item.company_url">
                            </div>
                            <div class="data">
                                <div class="title">{{item.company_name}}</div>
                                <div class="intro">{{item.business}}</div>
                            </div>
                            <div class="detail">
                                <span v-if="item.is_authentication === 1" class="tag"></span>
                                <span v-else-if="item.is_authentication === 2" class="gao_tag"></span>
                                <span v-else class="w_tag"></span>
                                <span class="icon" @click="tobusiness(item.id)"></span>
                            </div>
                        </div>
                        <div class="item-line"></div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
    import {Toast} from 'mint-ui'
    export default {
        data () {
            return {
                api: '',
                id: '',
                id2: '',
                category: [],
                tags: [],
                limit_show: false,
                result: []
            }
        },
        watch: {
            '$route': function (val, oldval) {
                document.documentElement.style.fontSize =
                        document.documentElement.clientWidth / 7.5 + 'px'
                this.api = this.$store.state.api
                this.id = this.$store.state.id
                this.id2 = this.$store.state.id2
                this.getthree(this.id2)
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.api = this.$store.state.api
            this.id = this.$store.state.id
            this.id2 = this.$store.state.id2
            this.getthree(this.id2)
        },
        components: {},
        methods: {
            getthree (sid) {
                this.$http.post(this.api + '/index/user/get_three', {sid: sid}).then(function (response) {
                    this.category = response.body.data
                    if (this.category === '' ||
                            typeof (this.category) === 'undefined' || this.category.length < 1) {
                        this.limit_show = true
                        this.getother()
                    } else {
                        this.getbusiness(this.category[0].category_id, 0)
                    }
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                })
            },
            tobusiness (id) {
//                企业详情
                this.$router.push({path: '/business-index', query: {business_id: id}})
            },
            getbusiness (id3, index) {
                if (this.category === '' ||
                        typeof (this.category) === 'undefined') {
                    this.limit_show = true
                    return
                }
                if (index === '') {
                    for (var s = 0; s < this.category.length; s++) {
                        this.$set(this.tags, s, false)
                    }
                    this.limit_show = true
                } else {
                    this.limit_show = false
                    for (var i = 0; i < this.category.length; i++) {
                        if (i === index) {
                            this.$set(this.tags, i, true)
                        } else {
                            this.$set(this.tags, i, false)
                        }
                    }
                }
                var area = this.$store.state.province + this.$store.state.city + this.$store.state.district
                this.$http.get(this.api + '/index/user/index' +
                        '?area=' + area +
                        '&fid=' + this.id +
                        '&sid=' + this.id2 +
                        '&tid=' + id3).then(function (response) {
                    this.result = response.body.data
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                })
            },
            getother () {
                var area = this.$store.state.province + this.$store.state.city + this.$store.state.district
                this.$http.get(this.api + '/index/user/index' +
                        '?area=' + area +
                        '&fid=' + this.id +
                        '&sid=' + this.id2).then(function (response) {
                    this.result = response.body.data
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                })
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     width 7.5rem
     background-color #ffffff
     margin-top 1.79rem
     padding-bottom 0.98rem
     .line
      width 7.5rem
      background-color #f9f9f9
      height 0.09rem
     .type
      display flex
      font-size 0
      margin-bottom 0.35rem
      .desc
       color #333333
       set-size(16px)
       margin 0.48rem 0.19rem auto 0.29rem
      .values
       flex 1
       margin 0.18rem 1.18rem auto auto
       .value,.limit,.select-value,.select-limit
        set-size(15px)
        color #666666
        float left
        padding 0.13rem 0.25rem 0.12rem 0.25rem
        margin-top 0.18rem
        margin-left 0.21rem
       .value
        border 0.01rem solid #666666
        border-radius 0.1rem
       .select-value
        border 0.01rem solid #f9f9f9
        border-radius 0.1rem
        background-color #CBAC86
        color #ffffff
       .select-limit
        color #CBAC86
       .clearfix:after
        content '.'
        display inline-block
        height 0
        line-height 0
        clear both
        visibility hidden
     .lists-wrapper
      width 7.5rem
      .item-business
       display flex
       width 7.5rem
       justify-content space-between
       .image
        img
         width 1.01rem
         height 1.01rem
         background-repeat no-repeat
         background-size 1.01rem 1.01rem
         margin 0.36rem 0.17rem 0 0.29rem
       .data
        font-size 0
        margin-right 0.35rem
        width 100%
        max-width 100%
        .title
         color #333333
         set-size(15px)
         margin 0.44rem 00 0
        .intro
         color #666666
         set-size(12px)
         margin 0.23rem 0 0 0
       .detail
        margin-right 0.3rem
        .tag,.icon,.gao_tag
         background-repeat no-repeat
         display block
         font-size 0
        .tag,.w_tag,.gao_tag
         width 0.4rem
         height 0.47rem
         background-size 0.4rem 0.47rem
         margin 0.26rem auto
        .tag
         background-image url("pic_chuji.png")
        .w_tag
         display block
         margin-top 0
        .gao_tag
         background-image url("pic_gaoji.png")
        .icon
         width 1.81rem
         height 0.51rem
         margin 0.13rem auto
         background-size 1.81rem 0.51rem
         background-image url("btn_chakanxiangqing.png")
     .item-line
        width 7.5rem
        height 0.02rem
        background-color #d6d6d6
        margin-top 0.23rem
        display block
</style>



