<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div class="title">企业列表</div>
            </div>
            <div class="lists">
                <ul>
                    <li class="list" v-for="item in result">
                        <div class="title">
                            <div class="name">{{item.company_name}}</div>
                            <span v-show="item.is_authentication === 1" class="icon"></span>
                            <span v-show="item.is_authentication === 2" class="gao_icon"></span>
                        </div>
                        <div class="desc">{{item.business}}</div>
                        <div class="detail" @click="tobusiness(item.id)">
                            <span class="icon"></span>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
    import {Toast, Indicator} from 'mint-ui'
    export default {
        data () {
            return {
                api: '',
                result: []
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.api = this.$store.state.api
            Indicator.open()
            this.getlist()
        },
        components: {},
        methods: {
            tobusiness (id) {
//                企业详情
                this.$router.push({path: '/business-index', query: {business_id: id}})
            },
            getlist () {
                this.$http.post(this.api + '/index/index/search', {
                    skey: 1
                }).then(function (response) {
                    if (response.status === 200) {
                        this.result = response.body.data
                        Indicator.close()
                    }
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                    Indicator.close()
                })
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     position absolute
     bottom 1.03rem
     top 0
     overflow-y scroll
     -webkit-overflow-scrolling touch
     width 7.5rem
     .head
      width 7.5rem
      height 0.89rem
      display flex
      justify-content center
      background-color #141014
      .title
       color #CBAC86
       align-self center
       set-size(18px)
     .lists
      width 7.5rem
      .list
       width 7.5rem
       font-size 0
       position relative
       .title
        margin-left 0.29rem
        margin-top 0.35rem
        display flex
        .name
         display inline-block
         color #333333
         font-weight 600
         set-size(15px)
         white-space nowrap
         align-self center
         max-width 4.3rem
         overflow hidden
         text-overflow ellipsis
        .icon,.gao_icon
         align-self center
         display inline-block
         width 0.4rem
         height 0.47rem
         background-size 0.4rem 0.47rem
         background-repeat no-repeat
         margin-left 0.1rem
        .icon
         background-image url("pic_chuji.png")
        .gao_icon
         background-image url("pic_gaoji.png")
       .desc
        margin-left 0.29rem
        set-size(12px)
        color #999999
        margin-top 0.23rem
        white-space nowrap
        margin-bottom 0.23rem
        white-space nowrap
        overflow-y hidden
        text-overflow ellipsis
        margin-right 0.5rem
       .detail
        position absolute
        top 0
        right 0
        .icon
         display inline-block
         width 1.81rem
         height 0.51rem
         background-size 1.81rem 0.51rem
         background-repeat no-repeat
         background-image url("btn_chakanxiangqing.png")
         margin-right 0.31rem
</style>



