<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div></div>
                <div class="title">{{title}}</div>
                <span v-if="!isShow" class="icon" @click="show"></span>
                <span v-else class="icon-h" @click="hide"></span>
            </div>
            <div class="content-wrapper">
                <div class="tabs">
                    <div class="tab-item" @click="tobusinesses">
                        <div :class="[busactive ? 'select-name' : 'name']">企业列表</div>
                        <div :class="[busactive ? 'under-line' : 'select-lin']"></div>
                    </div>
                    <div class="tab-item" @click="tonotice">
                        <div :class="[noactive ? 'select-name' : 'name']">公告栏</div>
                        <div :class="[noactive ? 'under-line' : 'select-lin']"></div>
                    </div>
                    <div class="tab-item" @click="tointro">
                        <div :class="[introactive ? 'select-name' : 'name']">工艺介绍</div>
                        <div :class="[introactive ? 'under-line' : 'select-lin']"></div>
                    </div>
                </div>
            </div>
            <div class="line"></div>
            <transition enter-active-class="animated slideInRight"
                        leave-active-class="animated slideOutRight">
             <div class="type-wrapper" v-if="isShow">
                <ul>
                    <li class="type" v-for="(item, index) in result"
                        @click="update(item.category_id, index)">
                        {{item.c_name}}
                    </li>
                </ul>
             </div>
            </transition>
            <transition enter-active-class="animated slideInDown"
                        leave-active-class="animated slideOutUp">
                <div class="intro-wrapper" v-if="introactive">
                    <div class="closed">
                        <span class="icon" @click="hideintro"></span>
                    </div>
                    <div class="content">
                        <div class="title">
                            {{intro.title}}
                        </div>
                        <div class="select-wrapper">
                            <div class="desc">
                                {{intro.content}}
                            </div>
                            <div class="next-wrapper">
                                <div class="left" @click="nextintro('1')">
                                    上一条
                                </div>
                                <div class="right" @click="nextintro('0')">
                                    下一条
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </transition>
            <router-view></router-view>
        </div>
        <navbar selectShow="company"></navbar>
    </div>
</template>
<script>
    import {Toast} from 'mint-ui'
    import NavBar from '../navbar/navbar.vue'
    export default {
        data () {
            return {
                api: '',
                isShow: false,
                busactive: true,
                noactive: false,
                introactive: false,
                title: '',
                result: [],
                id: '',
                id2: '',
                intro: {},
                tag: '',
                catagroy: 0,
                cate_tag: '',
                page: 1
            }
        },
        watch: {
            '$route': function (val, oldVal) {
                if (this.$route.path === '/engineer-index') {
                    document.documentElement.style.fontSize =
                            document.documentElement.clientWidth / 7.5 + 'px'
                    this.api = this.$store.state.api
                    this.title = this.$store.state.value
                    this.id = this.$store.state.id
                    this.id2 = this.$store.state.id2
                    this.tobusinesses()
                    this.getcassify()
                }
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.api = this.$store.state.api
            this.title = this.$store.state.value
            this.id = this.$store.state.id
            this.id2 = this.$store.state.id2
            this.tobusinesses()
            this.getcassify()
        },
        components: {
            navbar: NavBar
        },
        methods: {
            tobusinesses () {
                this.$router.replace({name: 'EngBusinessesIndex'})
                this.busactive = true
                this.noactive = false
                this.introactive = false
            },
            tonotice () {
                this.$router.replace({path: '/eng-notice-index'})
                this.busactive = false
                this.noactive = true
                this.introactive = false
            },
            tointro () {
                this.busactive = false
                this.noactive = false
                this.introactive = true
            },
            hide () {
                this.isShow = false
            },
            show () {
                this.isShow = true
            },
            hideintro () {
                this.introactive = false
                this.tobusinesses()
            },
            getcassify () {
                this.$http.get(this.api + '/index/user/category').then(function (response) {
                    this.result = response.body.data
                    this.getintro(this.id, this.page)
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 3000
                    })
                })
            },
            update (id, index) {
                this.$store.state.id = id
                this.$store.state.id2 = this.result[index].child[0].category_id
                this.$store.state.value = this.result[index].child[0].c_name
                this.title = this.$store.state.value
                this.id = this.$store.state.id
                this.id2 = this.$store.state.id2
                if (id === 1) {
//                    机械类
                    this.$router.replace({name: 'MachineIndex'})
                } else {
//                    非机械类
                    this.$router.replace({name: 'EngineerIndex'})
                }
                this.hide()
            },
            getintro (id, page) {
//                工艺介绍
                this.$http.post(this.api + '/index/user/technology', {
                    page: page,
                    fid: id
                }).then(function (response) {
                    this.catagroy = response.body.count
                    this.intro = response.body.data.tech
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 3000
                    })
                })
            },
            nextintro (tag) {
                if (tag === '1') {
//                    上一条
                    this.page --
                    if (this.page < 1) {
                        Toast({
                            message: '没有了',
                            position: 'bottom',
                            duration: 1000
                        })
                        this.page = 1
                        return
                    } else {
                        this.getintro(this.id, this.page)
                    }
                } else {
//                    下一条
                    this.page ++
                    if (this.page > this.catagroy) {
                        Toast({
                            message: '没有了',
                            position: 'bottom',
                            duration: 1000
                        })
                        this.page = this.catagroy
                        return
                    } else {
                        this.getintro(this.id, this.page)
                    }
                }
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     position absolute
     bottom 1.03rem
     top 0
     width 7.5rem
     background-color #ffffff
        padding-bottom 0.98rem
     .head
      width 7.5rem
      height 0.89rem
      background-color #141014
      display flex
      justify-content space-between
      position fixed
      right 0
      top 0
      bottom 0
      z-index 100
      .title
       align-self center
       set-size(18px)
       color #CBAC86
      .icon-h
       background-image url("icon_quxiao-.png")
      .icon
       background-image url("icon_Hamburger.png")
      .icon,.icon-h
       background-repeat no-repeat
       display inline-block
       align-self center
       margin-right 0.3rem
       width 0.26rem
       height 0.26rem
       background-size 0.26rem 0.26rem
     .content-wrapper
      width 7.5rem
      height 0.81rem
      position fixed
      margin-top 0.89rem
      .tabs
       width 7.5rem
       display flex
       background-color #ffffff
       .tab-item
        flex 1
        .name,.select-name
         margin-top 0.24rem
         text-align center
         set-size(18px)
        .name
         color #666666
        .select-name
         color #CBAC86
        .under-line,.select-line
         width 1.61rem
         height 0.05rem
         background-color #CBAC86
         margin 0.18rem auto
        .under-line
         visibility visible
        .select-lin
         visibility hidden
     .line
      width 7.5rem
      height 0.09rem
      background-color #f9f9f9
     .type-wrapper
      position fixed
      right 0
      top 0
      bottom 0
      z-index 101
      background-color #212121
      overflow auto
      height auto
      width 2.71rem
      margin-top 0.89rem
      margin-bottom 0.99rem
      .type
       set-size(15px)
       color #ffffff
       padding 0.27rem 0.61rem 0.26rem 0.62rem
       font-weight 600
       border-bottom 0.02rem solid #999999
     .intro-wrapper
      width 7.5rem
      height 100%
      position fixed
      top 0
      bottom 0
      overflow auto
      background-color rgba(7, 17, 27, 0.4)
      z-index 100
      font-size 0
      padding-top 0.6rem
      .closed
       width 7.5rem
       .icon
        width 0.61rem
        height 0.61rem
        background-repeat no-repeat
        background-size 0.61rem 0.61rem
        background-image url("icon_quxiao.png")
        display inline-block
        float right
        margin-right 0.6rem
      .content
       width 7.5rem
       display flex
       justify-content center
       flex-direction column
       .title
        width 4.41rem
        height 1.12rem
        background-repeat no-repeat
        background-size 4.41rem 1.12rem
        background-image url("pic_chengweiyouzhihehuoren.png")
        set-size(20px)
        color #FFFFFF
        align-self center
        text-align center
        line-height 1.12rem
       .select-wrapper
        width 5.11rem
        height 7.41rem
        background-repeat no-repeat
        background-size 5.11rem 7.41rem
        align-self center
        background-color #ffffff
        .next-wrapper
         position relative
         width 5.11rem
         height 1.05rem
         display flex
         justify-content center
         .left,.right
          set-size(15px)
          flex 1
          align-self center
          text-align center
          margin 0.12rem 0.37rem 0.11rem 0.37rem
          padding 0.12rem 0.37rem 0.11rem 0.37rem
          border 0.02rem solid #999999
          white-space nowrap
        .desc
         set-size(14px)
         color #666666
         align-self center
         overflow scroll
         height 5.42rem
         text-align center
         padding 0.47rem 0.4rem 0.47rem 0.36rem
</style>



