<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div class="title">
                    {{title}}
                </div>
            </div>
            <div class="input-wrapper">
                <input class="text" :placeholder="holder" v-model="value">
            </div>
            <div class="hint">
                {{hint}}
            </div>
            <div v-if="type === '0'" class="btn-wrapper" @click="updatename">
                保存
            </div>
            <div v-else class="btn-wrapper" @click="updateaddress">
                保存
            </div>
        </div>
    </div>
</template>
<script>
    import { Toast } from 'mint-ui'
    export default {
        data () {
            return {
                title: '修改企业',
                holder: '未填写',
                hint: '*请仔细填写相关的企业信息',
                value: '',
                api: '',
                user_id: '',
                type: '0'
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.api = this.$store.state.api
            this.user_id = this.$store.state.user_id
            this.type = this.$route.params.type
            this.value = this.$route.params.value
            if (this.type === '0') {
                this.title = '修改企业'
                this.holder = '未填写'
                this.hint = '*请仔细填写相关的企业信息'
            } else {
                this.title = '修改地址'
                this.holder = '详细地址'
                this.hint = '*请仔细填写相关的地址信息'
            }
        },
        components: {},
        methods: {
            updatename () {
//                修改企业
                if (this.value === '' ||
                        typeof (this.value) === 'undefined' ||
                        this.value.indexOf(' ') !== -1) {
                    Toast({
                        message: '企业名不符合标准',
                        position: 'bottom',
                        duration: 1000
                    })
                    return
                }
                this.$http.post(this.api + '/index/my/update_name', {
                    user_id: this.user_id,
                    company_name: this.value}).then(function (response) {
                    if (response.body.status === 200) {
                        Toast({
                            message: '修改成功',
                            position: 'bottom',
                            duration: 1000
                        })
                        this.$router.go(-1)
                    } else {
                        Toast({
                            message: '修改失败',
                            position: 'bottom',
                            duration: 1000
                        })
                    }
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                })
            },
            updateaddress () {
//                修改企业地址
                if (this.value === '' ||
                        typeof (this.value) === 'undefined' ||
                        this.value.indexOf(' ') !== -1) {
                    Toast({
                        message: '企业地址不符合标准',
                        position: 'bottom',
                        duration: 1000
                    })
                    return
                }
                this.$http.post(this.api + '/index/my/update_address', {
                    user_id: this.user_id,
                    company_address: this.value}).then(function (response) {
                    if (response.body.status === 200) {
                        Toast({
                            message: '修改成功',
                            position: 'bottom',
                            duration: 1000
                        })
                        this.$router.go(-1)
                    } else {
                        Toast({
                            message: '修改失败',
                            position: 'bottom',
                            duration: 1000
                        })
                    }
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                })
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     width 7.5rem
     height 100%
     background-color #f9f9f9
     .head
      width 7.5rem
      height 0.89rem
      background-color #141014
      display flex
      justify-content center
      .title
       align-self center
       color #CBAC86
       set-size(18px)
     .input-wrapper
      width 6.9rem
      margin-left 0.17rem
      .text
       width 6.9rem
       margin 0.45rem auto
       background-color #ffffff
       height 0.88rem
       padding-left 0.17rem
     .hint
      color #999999
      set-size(14px)
      width 7.5rem
      text-align center
      margin-top 0.24rem
     .btn-wrapper
      width 6.91rem
      margin 0.48rem auto
      height 0.81rem
      background-color #DAC098
      color #ffffff
      set-size(18px)
      text-align center
      line-height 0.81rem
      box-shadow 0.1rem 0.1rem 0.05rem #ece6dc
      border-radius 0.1rem
</style>



