<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div class="name">企业分类</div>
                <span class="icon" @click="tosearch"></span>
            </div>
            <div class="classify-wrapper">
                <ul>
                    <li class="content-wrapper" v-for="(item, index) in result">
                        <div class="line"></div>
                        <div class="content">
                            <div class="left">
                                <div class="classify">
                                    <div class="title">
                                        {{item.c_name}}
                                    </div>
                                </div>
                            </div>
                            <div class="right">
                                <ul>
                                    <li class="values-wrapper clearfix"
                                        @click="tomachine(item.category_id, item2.category_id, item2.c_name)"
                                        v-for="item2 in item.child">
                                        {{item2.c_name}}
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
        <navbar selectShow="company"></navbar>
    </div>
</template>
<script>
    import NavBar from '../navbar/navbar.vue'
    import {Toast} from 'mint-ui'
    export default {
        data () {
            return {
                api: '',
                result: []
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.api = this.$store.state.api
            this.getcassify()
        },
        components: {
            navbar: NavBar
        },
        methods: {
            tomachine (id, id2, name) {
                if (id === 1) {
//                    机械类
                    this.$router.push({name: 'MachineIndex'})
                } else {
//                    非机械类
                    this.$router.push({name: 'EngineerIndex'})
                }
                this.$store.commit('setFid', id)
                this.$store.commit('setSid', id2)
                this.$store.commit('setValue', name)
            },
            tosearch () {
                this.$router.push({path: '/search-index'})
            },
            getcassify () {
                this.$http.get(this.api + '/index/user/category').then(function (response) {
                    this.result = response.body.data
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 3000
                    })
                })
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     position absolute
     bottom 1.03rem
     top 0
     overflow-y scroll
     -webkit-overflow-scrolling touch
     width 7.5rem
     .head
      width 7.5rem
      height 0.89rem
      background-color #141014
      display flex
      justify-content space-between
      .name
       color #CBAC86
       set-size(18px)
       align-self center
       margin-left 0.3rem
      .icon
       display inline-block
       width 0.3rem
       height 0.3rem
       background-repeat no-repeat
       background-size 0.3rem 0.3rem
       background-image url("icon_sousuo.png")
       align-self center
       margin-right 0.29rem
     .classify-wrapper
      width 7.5rem
      .content-wrapper
       width 7.5rem
       .line
        width 7.5rem
        height 0.1rem
        background-color #f9f9f9
       .content
        width 7.5rem
        display flex
        font-size 0
        .left
         align-self center
         position relative
         margin 0.18rem 0 0.18rem 0.29rem
         width 1.25rem
         height 1.25rem
         background-color #DAC098
         background-repeat no-repeat
         background-size 1.25rem 1.25rem
         overflow-y hidden
         .classify
          position absolute
          left 0
          top 0
          height 1.25rem
          display flex
          justify-content center
          .title
           color #ffffff
           set-size(15px)
           line-height 0.34rem
           align-self center
           padding 0.15rem 0.13rem 0.14rem 0.17rem
        .right
         margin-left 0.2rem
         margin-top 0.21rem
         flex 1
         align-self center
         .values-wrapper
          color #999999
          set-size(15px)
          line-height 0.24rem
          border 0.02rem solid #999999
          padding 0.12rem 0.16rem 0.11rem 0.17rem
          margin-bottom 0.17rem
          margin-left 0.15rem
          float left
         .clearfix:after
          content '.'
          display inline-block
          height 0
          line-height 0
          clear both
          visibility hidden
</style>



