<template>
    <div>
        <div class="wrapper">
            <div class="line"></div>
            <div class="toast">
                <div class="title">电脑访问获得最佳编辑合同的体验</div>
            </div>
            <div class="content">
                <ul>
                    <li class="item" v-for="item in result">
                        <div class="title">
                            <div class="name">{{item.contract_name}}</div>
                            <div class="time">{{item.update_time}}</div>
                        </div>
                        <div class="desc" v-if="item.status === 0">等待上传</div>
                        <div class="desc" v-else-if="item.status === 101">等待对方确认合作</div>
                        <div class="desc" v-else-if="item.status === 102">我发是否确认合作</div>
                        <div class="desc" v-else-if="item.status === 103">被拒接</div>
                        <div class="desc" v-else-if="item.status === 104">已拒接</div>
                        <div class="desc" v-else-if="item.status === 200">合同签订中</div>
                        <div class="desc" v-else-if="item.status === 210">合同签订中有更新</div>
                        <div class="desc" v-else-if="item.status === 221">等待对方确认</div>
                        <div class="desc" v-else-if="item.status === 222">等待我确认</div>
                        <div class="desc" v-else-if="item.status === 300">合同签订成功</div>
                        <div class="desc" v-else>状态不明</div>
                        <div class="line"></div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
    import {Toast, Indicator} from 'mint-ui'
    export default {
        data () {
            return {
                user_id: '',
                api: '',
                result: []
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.user_id = this.$store.state.user_id
            this.api = this.$store.state.api
            Indicator.open()
            this.getconfirm()
        },
        components: {},
        methods: {
            getconfirm () {
                this.$http.post(this.api + '/index/my/status', {
                    user_id: this.user_id
                }).then(function (response) {
                    this.result = response.body.data
                    Indicator.close()
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 3000
                    })
                    Indicator.close()
                })
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../../common/stylus/base.styl"
    .wrapper
     width 7.5rem
     .line
      width 7.5rem
      height 0.09rem
      background-color #f9f9f9
     .toast
      width 7.5rem
      height 1.31rem
      display flex
      background-color #CBAC86
      justify-content center
      .title
       color #ffffff
       set-size(18px)
       align-self center
     .content
      width 7.5rem
      margin-top 0.09rem
      .item
       width 7.5rem
       font-size 0
       .title
        display flex
        justify-content space-between
        .name
         color #333333
         set-size(16px)
         margin-top 0.45rem
         margin-left 0.3rem
         white-space nowrap
         overflow-y hidden
        .time
         color #999999
         set-size(14px)
         margin-top 0.51rem
         margin-right 0.31rem
       .desc
        color #999999
        set-size(14px)
        margin 0.24rem 0 0.41rem 0.3rem
       .line
        width 6.91rem
        height 0.02rem
        background-image url("line.png")
        margin 0 auto
</style>



