import Vue from 'vue'
import Router from 'vue-router'
/*
 * 首页
 */
import homeIndex from '../components/home/index.vue'
/*
 * 企业和班组
 */
import CompanyIndex from '../components/company/index.vue'
/*
 * 个人中心
 */
import PersonalIndex from '../components/personal/index.vue'
/*
 *企业详情
 */
import BusinessIndex from '../components/business/index.vue'
/*
 *类别-企业列表
 */
import BusinessesIndex from '../components/businesses/index.vue'
/*
 *企业列表
 */
import BusinessesListsIndex from '../components/businesslists/index.vue'
/*
 *公告栏
 */
import NoticeIndex from '../components/noticeboard/index.vue'
/*
 *我的公告
 */
import MyNoticeIndex from '../components/notice/index.vue'
/*
 * 机械租赁企业
 */
import MachineIndex from '../components/machine/index.vue'
/*
 * 工程企业
 */
import EngineerIndex from '../components/engineering/index.vue'
/*
 * 我的账户
 */
import AccountIndex from '../components/account/index.vue'
/*
 * 我的-评价
 */
import CommentIndex from '../components/comment/index.vue'
/*
 * 我的-收到的评价
 */
import ReceiveIndex from '../components/comment/receive/index.vue'
/*
 * 我的-发出的评价
 */
import SendIndex from '../components/comment/send/index.vue'
/*
 * 我的收藏
 */
import CollectIndex from '../components/collection/index.vue'
/*
 * 我的客户
 */
import CustomerIndex from '../components/customer/index.vue'
/*
 * 确认关系
*/
import ConfirmingIndex from '../components/customer/confirming/index.vue'
/*
 * 查看合同
 */
import ContractIndex from '../components/customer/contract/index.vue'
/*
 * 新闻详情
 */
import NewsIndex from '../components/news/index.vue'
/*
 * 搜索页
 */
import SearchIndex from '../components/search/index.vue'
/*
 * 登陆
 */
import LoginIndex from '../components/user/login/index.vue'
/*
 * 注册、验证
 */
import RegisterIndex from '../components/user/register/index.vue'
/*
 * 密码设置、密码重置
 */
import PassIndex from '../components/user/password/index.vue'
/*
 * 修改-地址、企业名
 */
import ReviseIndex from '../components/revise/index.vue'

/*
 * 企业资料
 */
import DataIndex from '../components/companydata/index.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: homeIndex
    },
    {
      path: '/home-index',
      name: 'homeIndex',
      component: homeIndex
    },
    {
      path: '/machine-index',
      name: 'MachineIndex',
      component: MachineIndex,
      children: [
        {
          path: '/businesses-index',
          name: 'BusinessesIndex',
          component: BusinessesIndex
        },
        {
          path: '/notice-index',
          name: 'NoticeIndex',
          component: NoticeIndex
        }
      ]
    },
    {
      path: '/engineer-index',
      name: 'EngineerIndex',
      component: EngineerIndex,
      children: [
        {
          path: '/eng-businesses-index',
          name: 'EngBusinessesIndex',
          component: BusinessesIndex
        },
        {
          path: '/eng-notice-index',
          name: 'EngNoticeIndex',
          component: NoticeIndex
        }
      ]
    },
    {
      path: '/company-index',
      name: 'CompanyIndex',
      component: CompanyIndex
    },
    {
      path: '/personal-index',
      name: 'PersonalIndex',
      component: PersonalIndex
    },
    {
      path: '/business-index',
      name: 'BusinessIndex',
      component: BusinessIndex
    },
    {
      path: '/account-index',
      name: 'AccountIndex',
      component: AccountIndex
    },
    {
      path: '/myNotice-index',
      name: 'MyNoticeIndex',
      component: MyNoticeIndex
    },
    {
      path: '/comment-index',
      name: CommentIndex,
      component: CommentIndex,
      children: [
        {
          path: '/receive-index',
          name: 'ReceiveIndex',
          component: ReceiveIndex
        },
        {
          path: '/send-index',
          name: 'SendIndex',
          component: SendIndex
        }
      ]
    },
    {
      path: '/collect-index',
      name: 'CollectIndex',
      component: CollectIndex
    },
    {
      path: '/customer-index',
      name: 'CustomerIndex',
      component: CustomerIndex,
      children: [
        {
          path: '/confirming-index',
          name: 'ConfirmingIndex',
          component: ConfirmingIndex
        },
        {
          path: '/contract-index',
          name: 'ContractIndex',
          component: ContractIndex
        }
      ]
    },
    {
      path: '/news-index',
      name: 'NewsIndex',
      component: NewsIndex
    },
    {
      path: '/search-index',
      name: 'SearchIndex',
      component: SearchIndex
    },
    {
      path: '/login-index',
      name: 'LoginIndex',
      component: LoginIndex
    },
    {
      path: '/register-Index',
      name: 'RegisterIndex',
      component: RegisterIndex
    },
    {
      path: '/pass-index',
      name: 'PassIndex',
      component: PassIndex
    },
    {
      path: '/businesslists-index',
      name: 'BusinessesListsIndex',
      component: BusinessesListsIndex
    },
    {
      path: '/revise-index',
      name: 'ReviseIndex',
      component: ReviseIndex
    },
    {
      path: '/data-index',
      name: 'DataIndex',
      component: DataIndex
    }
  ]
})



