<template>
    <div id="container">
        <div class="wrapper">
            <div class="location">
                <div class="left" @click="showlocation">
                    <span class="icon"></span>
                    <div class="address">{{province}}{{city}}{{district}}</div>
                </div>
                <div class="right" @click="tosearch">
                    <span class="icon"></span>
                </div>
            </div>
            <div class="carousel">
                <swiper :options="swiperOption" ref="mySwiperA">
                    <swiper-slide v-for="(item, index) in pics" :key="index">
                        <div>
                            <img :src="api+item.banner" @click="piclick">
                        </div>
                    </swiper-slide>
                </swiper>
            </div>
            <div class="notice">
                <span class="icon"></span>
                <div class="desc">
                    <marquee direction="left" scrolldelay="150">
                        {{broadcast}}
                    </marquee>
                </div>
            </div>
            <div class="line"></div>
            <div class="category">
                <div class="title">企业班组分类搜索</div>
                <div class="content">
                    <div class="machine"
                         v-for="(item, index) in category"
                         @click="update(item.category_id, index)">
                        <img class="icon" :src="api+item.c_pic">
                        <div class="desc">{{item.c_name}}</div>
                    </div>
                    <div class="more" @click="tocompany">
                        <span class="icon"></span>
                        <div class="desc">查看更多</div>
                    </div>
                </div>
            </div>
            <div class="line"></div>
            <div class="lists-wrapper">
                <div class="more">
                    <div class="left">
                        <div class="zh-sign">优质企业</div>
                        <div class="e-sign">QUALITY ENTERPRISES</div>
                    </div>
                    <div class="right">
                        <span class="icon" @click="tobusinesslists"></span>
                    </div>
                </div>
                <div class="lists">
                    <ul>
                        <li class="list" v-for="item in company">
                            <div class="title">
                                <div class="name">{{item.company_name}}</div>
                                <span class="icon" v-show="item.is_authentication === 1"></span>
                                <span class="gao_icon" v-show="item.is_authentication === 2"></span>
                            </div>
                            <div class="desc">{{item.business}}</div>
                            <div class="detail" @click="tobusiness(item.id)">
                                <span class="icon"></span>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="line"></div>
            <div class="news-wrapper">
                <div class="title">
                    <div class="zh-sign">行业新闻</div>
                    <div class="en-sign">INDUSTRY NEWS</div>
                </div>
                <div class="news">
                    <ul>
                        <li class="content" @click="tonews(item.id)" v-for="item in newsContent">
                            <div class="icon">
                                <img :src="api+item.img">
                            </div>
                            <div class="desc">{{item.title}}</div>
                            <div class="date">
                                <div class="tag">发布日期</div>
                                <div class="time">{{item.time}}</div>
                            </div>
                            <div class="clearfix"></div>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="clear"></div>
            <vue-area :props-show="isShow" :props-result="result" @result="areaResult"></vue-area>
        </div>
        <navbar selectShow="home"></navbar>
    </div>
</template>
<script>
    import AMap from 'AMap'
    import Navbar from '../navbar/navbar.vue'
    import {Toast, Indicator} from 'mint-ui'
    import vueArea from 'vue-area'
    export default {
        data () {
            return {
                api: '',
                isShow: false,
                province: '',
                city: '',
                district: '',
                broadcast: '',
                pics: [],
                result: {},
                swiperOption: {
                    autoplay: 1000,
                    direction: 'horizontal',
                    loop: true,
                    autoplayDisableOnInteraction: false,
                    grabCursor: true,
                    setWrapperSize: true,
                    autoHeight: false,
                    pagination: '.swiper-pagination',
                    paginationType: 'bullets',
                    paginationClickable: true,
                    mousewheelControl: false,
                    observeParents: true,
                    paginationBulletRender: function (swiper, index, className) {
                        return '<span style="width: 5px; height: 5px;" class="' + className + '"></span>'
                    }
                },
                swiperSlides: [1, 2, 3, 4, 5],
                company: [],
                category: [],
                newsContent: []
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            document.title = '土木工程宝'
            this.api = this.$store.state.api
            this.getuser()
            this.notice()
            this.getbanner()
            this.news()
            if (this.$store.state.location) {
                Indicator.open()
                this.geoLocation()
                this.$store.state.location = false
            } else {
                this.province = this.$store.state.province
                this.city = this.$store.state.city
                this.district = this.$store.state.district
                this.getcompany()
            }
        },
        computed: {
            swiper () {
                return this.$refs.mySwiperA.swiper
            }
        },
        mounted () {
            setInterval(() => {
                let swiperSlides = this.swiperSlides
                if (swiperSlides.length < 10) swiperSlides.push(swiperSlides.length + 1)
            }, 1000)
        },
        components: {
            navbar: Navbar,
            vueArea
        },
        methods: {
            getuser () {
              const obj = this
              this.$http.get(this.api + '/index/index/user_id').then(function (response) {
                  var userId = response.body.user_id
                  obj.$store.commit('setUserid', userId)
              }, function (response) {
                  Toast({
                      message: 'error',
                      position: 'bottom',
                      duration: 1000
                  })
              })
            },
            areaResult (show, result) {
                this.isShow = show
                this.province = result.province.name
                this.city = result.city.name
                this.district = result.area.name
                this.$store.state.province = this.province
                this.$store.state.city = this.city
                this.$store.state.district = this.district
                Indicator.open()
                this.getcompany()
            },
            tobusiness (id) {
//                企业详情
                this.$router.push({path: '/business-index', query: {business_id: id}})
            },
            tonews (id) {
//                新闻详情
                this.$router.push({name: 'NewsIndex', params: {news_id: id}})
            },
            tosearch () {
                this.$router.push({path: '/search-index'})
            },
            update (id, index) {
                if (id === 1) {
//                    机械类
                    this.$router.push({name: 'MachineIndex'})
                } else {
//                    非机械类
                    this.$router.push({name: 'EngineerIndex'})
                }
                this.$store.commit('setFid', id)
                this.$store.commit('setSid', this.category[index].child[0].category_id)
                this.$store.commit('setValue', this.category[index].child[0].c_name)
            },
            hidelocation () {
                this.isShow = false
            },
            showlocation () {
                this.isShow = true
            },
            tobusinesslists () {
                this.$router.push({path: '/businesslists-index'})
            },
            tocompany () {
                this.$router.push({path: '/company-index'})
            },
            geoLocation () {
                var map, geolocation
                map = new AMap.Map('container', {
                    resizeEnable: true
                })
                map.plugin('AMap.Geolocation', function () {
                    geolocation = new AMap.Geolocation({
                        enableHighAccuracy: true,
                        timeout: 5000,
                        maximumAge: 0,
                        convert: true,
                        buttonPosition: 'LB',
                        buttonOffset: new AMap.Pixel(10, 20)
                    })
                })
                map.addControl(geolocation)
                const obj = this
                geolocation.getCurrentPosition(function (status, result) {
                    if (result.info === 'SUCCESS') {
                        Indicator.close()
                        if (result.hasOwnProperty('addressComponent')) {
                            let address = result.addressComponent
                            obj.province = address.province
                            obj.city = address.city
                            obj.district = address.district
                            obj.$store.state.province = address.province
                            obj.$store.state.city = address.city
                            obj.$store.state.district = address.district
                        } else {
                            Toast({
                                message: '定位失败,请点击左上角选择',
                                position: 'center',
                                duration: 2000
                            })
                            obj.province = '省'
                            obj.city = '市'
                            obj.district = '区'
                        }
                    } else {
                        Indicator.close()
                        Toast({
                            message: result.message,
                            position: 'bottom',
                            duration: 1000
                        })
                    }
                    obj.getcompany()
                })
            },
            notice () {
//                平台公告
                this.$http.get(this.api + '/index/index/notice').then(function (response) {
                        this.broadcast = response.body
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                })
            },
            news () {
//                新闻
                this.$http.get(this.api + '/index/index/news').then(function (response) {
                    if (response.status === 200) {
                        this.newsContent = response.body
                    }
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                })
            },
            getbanner () {
//                轮播图 + 分类
                this.$http.get(this.api + '/index/index/index').then(function (response) {
                    if (response.status === 200) {
                        this.pics = response.body.pic
                        this.category = response.body.data
                    }
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                })
            },
            getcompany () {
//                优质企业
                let area = this.province + this.city + this.district
                this.$http.get(this.api + '/index/index/company' +
                        '?area=' + area).then(function (response) {
                    if (response.status === 200) {
                        this.company = response.body.company
                    }
                    Indicator.close()
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                    Indicator.close()
                })
                Indicator.close()
            },
            piclick (id) {
                console.log('data')
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     position absolute
     bottom 1.03rem
     top 0
     overflow-y scroll
     -webkit-overflow-scrolling touch
     width 7.5rem
     .location
      width 7.5rem
      height 0.89rem
      background-color #141014
      font-size 0
      display flex
      justify-content space-between
      .left
       align-self center
       display flex
       .icon
        background-image url("icon_add.png")
        width 0.32rem
        height 0.4rem
        background-size 0.32rem 0.4rem
        background-repeat no-repeat
        margin 0 0.15rem 0 0.3rem
        align-self center
       .address
        color #CBAC86
        display inline-block
        set-size(18px)
        white-space nowrap
        text-overflow ellipsis
        overflow hidden
        width 5.83rem
        align-self center
        padding-top 0.08rem
      .right
       align-self center
       display inline-block
       .icon
        display inline-block
        width 0.3rem
        height 0.3rem
        background-size 0.3rem 0.3rem
        background-image url("icon_sousuo.png")
        background-repeat no-repeat
        margin-right 0.3rem
     .carousel
      width 7.5rem
      height 3.9rem
      background-repeat no-repeat
      background-size 7.5rem 3.9rem
      div img
       width 7.5rem
       height 3.9rem
     .notice
      width 7.5rem
      height 0.81rem
      display flex
      .icon
       flex 0 0 0.4rem
       align-self center
       display inline-block
       background-image url("icon_guangbo.png")
       width 0.4rem
       height 0.4rem
       background-size 0.4rem 0.4rem
       background-repeat no-repeat
       margin-left 0.3rem
       margin-right 0.22rem
      .desc
       flex 1
       display inline-block
       align-self center
       set-size(15px)
       color #999999
       overflow auto
       text-overflow marquee
       marquee
        padding-top 0.04rem
     .line
      width 7.5rem
      height 0.31rem
      background-color #f9f9f9
     .category
      width 7.5rem
      height auto
      padding-bottom  0.39rem
      .title
       display inline-block
       color #333333
       font-weight 600
       set-size(18px)
       margin-left 0.29rem
       margin-top 0.29rem
      .content
       display flex
       width 7.5rem
       margin-top 0.41rem
       overflow auto
       .machine,.more
        flex 1
        .icon
         width 1.31rem
         height 1.31rem
         display block
         background-size 1.31rem 1.31rem
         background-repeat no-repeat
         margin 0 auto
        .desc
         color #999999
         set-size(15px)
         margin-top 0.18rem
         text-align center
       .more
        .icon
         display block
         background-image url("icon_chakangengduo.png")
         margin 0 auto
     .lists-wrapper
      width 7.5rem
      height auto
      .more
       height 1.31rem
       width 7.5rem
       display flex
       justify-content space-between
       border-bottom 0.05rem solid #f9f9f9
       .left
        font-size 0
        margin-left 0.29rem
        align-self center
        .zh-sign
         set-size(18px)
         color #333333
         font-weight 600
        .e-sign
         margin-top 0.11rem
         set-size(12px)
         color #DAC098
       .right
        align-self center
        .icon
         display inline-block
         background-image url("btn_chakangengduo.png")
         width 1.81rem
         height 0.51rem
         background-size 1.81rem 0.51rem
         background-repeat no-repeat
         margin 0 0.31rem 0 auto
      .lists
       width 7.5rem
       .list
        width 7.5rem
        font-size 0
        position relative
        .title
         margin-left 0.29rem
         margin-top 0.35rem
         display flex
         .name
          display inline-block
          color #333333
          font-weight 600
          set-size(15px)
          white-space nowrap
          align-self center
          max-width 4.3rem
          overflow hidden
          text-overflow ellipsis
         .icon,.gao_icon
          align-self center
          display inline-block
          width 0.4rem
          height 0.47rem
          background-size 0.4rem 0.47rem
          background-repeat no-repeat
          margin-left 0.1rem
         .icon
          background-image url("pic_chuji.png")
         .gao_icon
          background-image url("pic_gaoji.png")
        .desc
         margin-left 0.29rem
         set-size(12px)
         color #999999
         margin-top 0.23rem
         white-space nowrap
         margin-bottom 0.23rem
         white-space nowrap
         overflow hidden
         text-overflow ellipsis
         margin-right 0.5rem
        .detail
         position absolute
         top 0
         right 0
         .icon
          display inline-block
          width 1.81rem
          height 0.51rem
          background-size 1.81rem 0.51rem
          background-repeat no-repeat
          background-image url("btn_chakanxiangqing.png")
          margin-right 0.31rem
     .news-wrapper
      width 7.5rem
      .title
       font-size 0
       margin-left 0.28rem
       margin-top 0.35rem
       .zh-sign
        color #333333
        set-size(18px)
        font-weight 600
       .en-sign
        color #CBAC86
        set-size(12px)
        margin-top 0.08rem
      .news
       width 7.5rem
       .content
        font-size 0
        float left
        margin-left 0.29rem
        .icon
         img
          width 3.3rem
          height 2.38rem
          margin-top 0.34rem
        .clearfix:after
         content '.'
         display inline-block
         height 0
         line-height 0
         clear both
         visibility hidden
        .desc
         margin-top 0.08rem
         color #666666
         width 3.08rem
         set-size(12px)
         overflow hidden
         white-space nowrap
         text-overflow ellipsis
        .date
         margin-top 0.16rem
         .tag
          color #999999
          set-size(10px)
          display inline-block
         .time
          color #999999
          set-size(10px)
          display inline-block
          margin-left 0.1rem
     .clear
      clear both
</style>



