<template>
    <div class="main">
        <div class="wrapper">
            <div class="head">
                <div class="title">{{title}}</div>
            </div>
            <div class="h-line"></div>
            <div class="logo">
                <span></span>
            </div>
            <div class="pass-wrapper">
                <div class="phone">
                    <span class="icon"></span>
                    <input type="password" v-model="pass1" class="input-pass" placeholder="输入您的密码">
                </div>
                <div class="z-line"></div>
            </div>
            <div class="forget-wrapper">
                <div class="phone">
                    <span class="icon"></span>
                    <input type="password" v-model="pass2" class="input-pass" placeholder="再次输入您的密码">
                </div>
                <div class="z-line"></div>
            </div>
            <div class="btn-wrapper">
                <span class="btn-icon" @click="verifyPass"></span>
            </div>
            <div v-if="isShow" class="desc">*两次输入的密码不一致</div>
        </div>
    </div>
</template>
<script>
    import { Toast } from 'mint-ui'
    export default {
        data () {
            return {
                api: '',
                pass1: '',
                pass2: '',
                isShow: false,
                phone: '',
                type: '',
                title: '设置密码'
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.api = this.$store.state.api
            this.phone = this.$route.params.phone
            this.type = this.$route.params.type
            if (this.type === '0') {
                this.title = '设置密码'
            } else {
                this.title = '重置密码'
            }
        },
        components: {},
        methods: {
            verifyPass () {
                if (this.pass1 === '' ||
                        typeof (this.pass1) === 'undefined' ||
                        this.pass2 === '' ||
                        typeof (this.pass2) === 'undefined') {
                    Toast({
                        message: '请输入密码',
                        position: 'bottom',
                        duration: 1000
                    })
                    return
                }
                if (this.pass1.indexOf(' ') !== -1 || this.pass1.indexOf(' ') !== -1) {
                    Toast({
                        message: '密码含有非法字符串',
                        position: 'bottom',
                        duration: 1000
                    })
                    return
                }
                if (this.pass1.length < 5 || this.pass1.length > 20) {
                    Toast({
                        message: '密码长度至少大于5小于20',
                        position: 'bottom',
                        duration: 1000
                    })
                    return
                }
                if (this.pass1 !== this.pass2) {
                    this.isShow = true
                } else {
                    this.isShow = false
                    this.$http.post(this.api + '/index/register/set',
                            {mobile: this.phone, pwd: this.pass1}
                    ).then(function (response) {
                        if (response.body.result === 200) {
                            Toast({
                                message: '注册成功',
                                position: 'bottom',
                                duration: 1000
                            })
//                            var userId = response.body.user_id
//                            this.$store.state.user_id = userId
                            this.$router.replace({path: '/home-index'})
                        } else {
                            Toast({
                                message: response.body.msg,
                                position: 'bottom',
                                duration: 1000
                            })
                        }
                    }, function (response) {
                        Toast({
                            message: 'error',
                            position: 'bottom',
                            duration: 1000
                        })
                    })
                }
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../../common/stylus/base.styl"
   .main
    width 7.5rem
    height 100%
    background-color #141014
    .wrapper
     width 7.5rem
     background-color #141014
     .head
      width 7.5rem
      height 0.89rem
      display flex
      justify-content center
      .title
       color #CBAC86
       align-self center
       set-size(18px)
     .h-line
      width 7.5rem
      height 0.02rem
      background-color #CBAC86
     .logo
      width 7.5rem
      span
       display block
       width 3.91rem
       height 0.83rem
       background-size 3.91rem 0.83rem
       background-repeat no-repeat
       margin 1.54rem auto 0 auto
       background-image url("pic_logo.png")
     .forget-wrapper
      padding-top 0.41rem
      width 7.5rem
      background-color #141014
     .pass-wrapper
      background-color #141014
      width 7.5rem
      padding-top 0.83rem
     .forget-wrapper,.pass-wrapper
      .phone
       width 7.5rem
       display flex
       justify-content center
       padding-bottom 0.17rem
       .icon
        width 0.38rem
        height 0.44rem
        background-repeat no-repeat
        background-size 0.38rem 0.44rem
        background-image url("icon_mima.png")
        align-self center
       .input-pass
        border none
        color #E4CDA4
        set-size(16px)
        background-color #141014
        margin-left 0.34rem
        align-self center
        padding-bottom 0
      .z-line
       width 5.41rem
       height 0.03rem
       margin 0 auto
       background-color #E4CCA3
     .btn-wrapper
      width 7.5rem
      text-align center
      padding-top 1.42rem
      background-color #141014
      .btn-icon
       width 5.41rem
       height 0.81rem
       background-image url("btn_queding.png")
       background-repeat no-repeat
       background-size 5.41rem 0.81rem
       display inline-block
     .desc
      background-color #141014
      padding-top 0.49rem
      width 7.5rem
      text-align center
      color #7F7261
      set-size(15px)
</style>



