<template>
    <div>
        <div class="wrapper">
            <div class="line"></div>
            <div class="toast">
                <div class="title">电脑访问获得最佳编辑合同的体验</div>
            </div>
            <div class="content">
                <ul>
                    <li class="item" v-for="item in result">
                        <div class="title">
                            <div class="name">{{item.company_name}}</div>
                            <span class="icon" @click="check"></span>
                        </div>
                        <div class="desc">
                            <div class="doc">{{item.contract_name}}</div>
                            <span class="time">更新时间：{{item.create_time}}</span>
                        </div>
                        <div class="line"></div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
    import {Toast, Indicator} from 'mint-ui'
    export default {
        data () {
            return {
                user_id: '',
                api: '',
                result: []
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.user_id = this.$store.state.user_id
            this.api = this.$store.state.api
            Indicator.open()
            this.getcontract()
        },
        components: {},
        methods: {
            check () {
                Toast({
                    message: '请在PC端查看',
                    position: 'bottom',
                    duration: 1000
                })
            },
            getcontract () {
                this.$http.post(this.api + '/index/my/hetong', {
                    user_id: this.user_id
                }).then(function (response) {
                    this.result = response.body.data
                    Indicator.close()
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 3000
                    })
                    Indicator.close()
                })
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../../common/stylus/base.styl"
    .wrapper
     width 7.5rem
     .line
      width 7.5rem
      height 0.09rem
      background-color #f9f9f9
     .toast
      width 7.5rem
      height 1.31rem
      display flex
      background-color #CBAC86
      justify-content center
      .title
       color #ffffff
       set-size(18px)
       align-self center
     .content
      width 7.5rem
      margin-top 0.1rem
      .item
       width 7.5rem
       font-size 0
       .title
        display flex
        justify-content space-between
        margin-top 0.54rem
        .name
         color #333333
         set-size(14px)
         align-self center
         margin-left 0.29rem
        .icon
         display inline-block
         background-image url("btn_chakanhetong.png")
         width 1.41rem
         height 0.53rem
         background-repeat no-repeat
         background-size 1.41rem 0.53rem
         align-self center
         margin-right 0.3rem
       .desc
        display flex
        justify-content space-between
        .doc
         color #999999
         set-size(14px)
         margin-top 0.3rem
         margin-left 0.29rem
        .time
         color #999999
         set-size(14px)
         margin-top 0.3rem
         margin-right 0.32rem
       .line
        width 6.91rem
        height 0.02rem
        background-image url("line.png")
        margin 0.42rem auto
</style>



