<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div class="left">
                    <input type="text" class="edit" placeholder="请输入内容" v-model="key" @keyup="getsearch">
                </div>
                <div class="right">
                   <div @click="back">取消</div>
                </div>
            </div>
            <div class="lists">
                <ul>
                    <li class="list" v-for="item in result">
                        <div class="title">
                            <div class="name">{{item.company_name}}</div>
                            <span v-show="item.is_authentication === 1" class="icon"></span>
                        </div>
                        <div class="desc">{{item.business}}</div>
                        <div class="detail" @click="tobusiness(item.id)">
                            <span class="icon"></span>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
    import {Toast} from 'mint-ui'
    export default {
        data () {
            return {
                api: '',
                key: '',
                result: [],
                province: '',
                city: '',
                district: ''
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.api = this.$store.state.api
            this.province = this.$store.state.province
            this.city = this.$store.state.city
            this.district = this.$store.state.district
        },
        components: {},
        methods: {
            tobusiness (id) {
//                企业详情
                this.$router.push({path: '/business-index', query: {business_id: id}})
            },
            back () {
                this.$router.go(-1)
            },
            getsearch () {
                let area = this.province + this.city + this.district
                this.$http.post(this.api + '/index/index/search', {
                        key: this.key, skey: 0, area: area
                }).then(function (response) {
                    if (response.status === 200) {
                        this.result = response.body.data
                    }
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                })
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     width 7.5rem
     .head
      width 7.5rem
      height 0.89rem
      background-color #141014
      display flex
      justify-content space-between
      .left
        align-self center
        display flex
        justify-content center
       .edit
        align-self center
        color #CBAC86
        set-size(15px)
        width 5.4rem
        height 0.54rem
        background-color #141014
        border 0.01rem solid #CBAC86
        border-radius 0.26rem
        margin-left 0.3rem
        padding-left 0.32rem
        outline none
      .right
       align-self center
       div
        color #CBAC86
        set-size(18px)
        line-height 0.26rem
        margin-right 0.36rem
     .lists
      width 7.5rem
      .list
       width 7.5rem
       font-size 0
       position relative
       .title
        margin-left 0.29rem
        margin-top 0.35rem
        display flex
        .name
         display inline-block
         color #333333
         font-weight 600
         set-size(15px)
         white-space nowrap
         align-self center
        .icon
         align-self center
         display inline-block
         width 0.4rem
         height 0.47rem
         background-size 0.4rem 0.47rem
         background-repeat no-repeat
         background-image url("pic_renzheng.png")
         margin-left 0.1rem
       .desc
        margin-left 0.29rem
        set-size(12px)
        color #999999
        margin-top 0.23rem
        white-space nowrap
        margin-bottom 0.23rem
        white-space nowrap
        overflow-y hidden
        text-overflow ellipsis
        margin-right 0.5rem
       .detail
        position absolute
        top 0
        right 0
        .icon
         display inline-block
         width 1.81rem
         height 0.51rem
         background-size 1.81rem 0.51rem
         background-repeat no-repeat
         background-image url("btn_chakanxiangqing.png")
         margin-right 0.31rem
</style>



