<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div class="title">我的客户</div>
            </div>
            <div class="content-wrapper">
                <div class="tabs">
                    <div class="tab-item" @click="toconfirming">
                        <div :class="[isconfirm ? 'select-name' : 'name']">确认关系</div>
                        <div :class="[isconfirm ? 'under-line' : 'select-lin']"></div>
                    </div>
                    <div class="tab-item" @click="tocontract">
                        <div :class="[iscontract ? 'select-name' : 'name']">查看合同</div>
                        <div :class="[iscontract ? 'under-line' : 'select-lin']"></div>
                    </div>
                </div>
            </div>
            <div class="line"></div>
            <router-view></router-view>
        </div>
        <navbar selectShow="personal"></navbar>
    </div>
</template>
<script>
    import NavBar from '../navbar/navbar.vue'
    export default {
        data () {
            return {
                isconfirm: true,
                iscontract: false
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
        },
        components: {
            navbar: NavBar
        },
        methods: {
            toconfirming () {
                this.$router.replace({path: '/confirming-index'})
                this.isconfirm = true
                this.iscontract = false
            },
            tocontract () {
                this.$router.replace({path: '/contract-index'})
                this.isconfirm = false
                this.iscontract = true
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     position absolute
     bottom 1.03rem
     top 0
     overflow-y scroll
     -webkit-overflow-scrolling touch
     width 7.5rem
     .head
      width 7.5rem
      height 0.89rem
      display flex
      justify-content center
      background-color #141014
      .title
       color #CBAC86
       align-self center
       set-size(18px)
     .content-wrapper
      width 7.5rem
      height 0.81rem
      .tabs
       width 7.5rem
       display flex
       .tab-item
        flex 1
        .name,.select-name
         margin-top 0.24rem
         text-align center
         set-size(18px)
        .name
         color #666666
        .select-name
         color #CBAC86
        .under-line,.select-line
         width 1.61rem
         height 0.05rem
         background-color #CBAC86
         margin 0.18rem auto
        .under-line
         visibility visible
        .select-lin
         visibility hidden
     .line
      width 7.5rem
      height 0.09rem
      background-color #f9f9f9
</style>



