<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div class="title">我的账户</div>
            </div>
            <div class="head-line"></div>
            <div class="tag">
                <div class="left">
                    <span class="icon-zh"></span>
                    <div class="desc">我的账号</div>
                </div>
                <div class="right">
                    <div class="value">{{tel}}</div>
                    <span class="icon-zh" @click="exit"></span>
                </div>
            </div>
            <div class="tag">
                <div class="left">
                    <span class="icon-pj"></span>
                    <div class="desc">剩余评价次数</div>
                </div>
                <div class="right">
                    <div class="value">{{time}}次</div>
                    <span class="icon-npj"></span>
                </div>
            </div>
            <div class="tag">
                <div class="left">
                    <span class="icon-mc"></span>
                    <div class="desc">企业名称</div>
                </div>
                <div class="right">
                    <div class="value">{{company_name}}</div>
                    <span class="icon-mc" @click="revise('0')"></span>
                </div>
            </div>
            <div class="tag">
                <div class="left">
                    <span class="icon-dz"></span>
                    <div class="desc">企业地址</div>
                </div>
                <div class="right">
                    <div class="value-dz">{{company_address}}</div>
                    <span class="icon-dz" @click="revise('1')"></span>
                </div>
            </div>
            <div class="line"></div>
        </div>
        <navbar selectShow="personal"></navbar>
    </div>
</template>
<script>
    import NavBar from '../navbar/navbar.vue'
    import { Toast, Indicator, MessageBox } from 'mint-ui'
    export default {
        data () {
            return {
                api: '',
                user_id: '',
                company_name: '',
                company_address: '',
                tel: '',
                time: ''
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.api = this.$store.state.api
            this.user_id = this.$store.state.user_id
            Indicator.open()
            this.getmydata()
        },
        components: {
            navbar: NavBar
        },
        methods: {
            revise (type) {
                if (type === '0') {
                    this.$router.push({name: 'ReviseIndex', params: {type: type, value: this.company_name}})
                } else {
                    this.$router.push({name: 'ReviseIndex', params: {type: type, value: this.company_address}})
                }
            },
            getmydata () {
                this.$http.post(this.api + '/index/my/myinfo', {
                    user_id: this.user_id
                }).then(function (response) {
                    this.company_address = response.body.company_address
                    this.company_name = response.body.company_name
                    this.tel = response.body.tel
                    this.time = response.body.time
                    Indicator.close()
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 3000
                    })
                    Indicator.close()
                })
            },
            exit () {
                const obj = this
                MessageBox.confirm('确定退出当前账号').then(function () {
                    obj.$store.state.user_id = 0
                    obj.out()
                }, function () {})
            },
            out () {
                const obj = this
                this.$http.get(this.api + '/index/login/login_out').then(function (response) {
                    if (response.body.status === 200) {
                        Toast({
                            message: '退出成功',
                            position: 'bottom',
                            duration: 3000
                        })
                        obj.$router.replace({path: '/home-index'})
                    } else {
                        Toast({
                            message: '错误码' + response.body.status,
                            position: 'bottom',
                            duration: 3000
                        })
                    }
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 3000
                    })
                })
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     width 7.5rem
     position absolute
     bottom 1.03rem
     top 0
     overflow-y scroll
     -webkit-overflow-scrolling touch
     .head-line
      width 7.5rem
      height 0.1rem
      background-color #f9f9f9
     .head
      width 7.5rem
      height 0.89rem
      background-color #141014
      display flex
      justify-content center
      .title
       align-self center
       color #CBAC86
       set-size(18px)
     .tag
      width 7.5rem
      display flex
      justify-content space-between
      font-size 0
      .left
        display flex
        margin 0.32rem 0 0.27rem 0.3rem
       .icon-zh,.icon-pj,.icon-mc,.icon-dz
        align-self center
        display inline-block
        background-repeat no-repeat
       .icon-zh
        width 0.32rem
        height 0.3rem
        background-size 0.32rem 0.3rem
        background-image url("icon_wodezhanghao.png")
       .icon-pj
        width 0.3rem
        height 0.3rem
        background-size 0.3rem 0.3rem
        background-image url("icon_pingjiashu.png")
       .icon-mc
        width 0.32rem
        height 0.28rem
        background-image url("icon_qiyemingcheng.png")
        background-size 0.32rem 0.28rem
       .icon-dz
        width 0.26rem
        height 0.3rem
        background-size 0.26rem 0.3rem
        background-image url("icon_qiyedizhi.png")
       .desc
        display inline-block
        color #333333
        set-size(16px)
        align-self center
        margin-left 0.21rem
        white-space nowrap
        overflow-y hidden
      .right
       display flex
       margin-right 0.25rem
       .icon-zh,.icon-npj,.icon-mc,.icon-dz
        align-self center
        display inline-block
        width 0.79rem
        height 0.53rem
        background-repeat no-repeat
        background-size 0.79rem 0.53rem
       .icon-zh
        background-image url("btn_tuichu.png")
       .icon-mc,.icon-dz
        background-image url("btn_xiugai.png")
       .value,.value-dz
        display inline-block
        color #999999
        set-size(14px)
        align-self center
        margin-right 0.23rem
        white-space nowrap
        overflow hidden
        text-overflow ellipsis
        max-width 3rem
       .value-dz
        margin-left 0.42rem
    .line
     width 7.5rem
     height 0.02rem
     background-color #f9f9f9
</style>



