<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div class="title">查看公告</div>
            </div>
            <div class="content-wrapper">
                <ul>
                    <li class="item" v-for="(item, index) in result">
                        <div class="title">
                            <div class="name">{{item.user_name}}</div>
                            <div class="time">{{item.create_time}}</div>
                        </div>
                        <div class="desc">
                            {{item.content}}
                        </div>
                        <div class="images">
                            <ul>
                                <li class="image clearfix" v-for="url in item.notice_url">
                                    <img :src="api+url" @click="preview(api+url, index)">
                                </li>
                            </ul>
                            <div class="clear"></div>
                            <!--<div v-show="isShow[index]" class="preview-wrapper">-->
                                <!--<img :src="iv">-->
                            <!--</div>-->
                            <bigimg v-show="isShow[index]"  @clickit="canclePreview(index)" :imgSrc="iv" ></bigimg>
                        </div>
                        <div class="set-video" v-if="item.notice_video !== '' ">
                            <iframe  :src="item.notice_video"></iframe>
                        </div>
                        <div v-else></div>

                        <div class="step">
                            <span class="icon-del" @click="delnotice(item.id)"></span>
                            <span class="icon-che" @click="edit"></span>
                        </div>
                        <div class="line"></div>
                    </li>
                </ul>
            </div>
        </div>
        <navbar selectShow="personal"></navbar>
    </div>
</template>
<script>
    import NavBar from '../../components/navbar/navbar.vue'
    import { Toast, Indicator } from 'mint-ui'
    import BigImg from '../public/bigimg.vue'
    export default {
        data () {
            return {
                api: '',
                user_id: '',
                result: [],
                iv: '',
                isShow: []
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.api = this.$store.state.api
            this.user_id = this.$store.state.user_id
            Indicator.open()
            this.getdata()
        },
        components: {
            navbar: NavBar,
            'bigimg': BigImg
        },
        methods: {
            preview (url, index) {
                this.iv = url
                for (var i = 0; i < this.result.length; i++) {
                    if (index === i) {
                        this.$set(this.isShow, index, true)
                    } else {
                        this.$set(this.isShow, i, false)
                    }
                }
            },
            getdata () {
                this.$http.post(this.api + '/index/my/notice', {
                    user_id: this.user_id
                }).then(function (response) {
                    this.result = response.body.data
                    Indicator.close()
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 3000
                    })
                    Indicator.close()
                })
            },
            edit () {
                Toast({
                    message: '建议在PC官网上编辑',
                    position: 'center',
                    duration: 3000
                })
            },
            delnotice (id) {
                this.$http.post(this.api + '/index/my/del_notice', {id: id}).then(function (response) {
                        Toast({
                            message: '删除成功',
                            position: 'bottom',
                            duration: 1000
                        })
                        this.getdata()
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 3000
                    })
                })
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     position absolute
     bottom 1.03rem
     top 0
     overflow-y scroll
     -webkit-overflow-scrolling touch
     width 7.5rem
     .head
      width 7.5rem
      height 0.89rem
      display flex
      justify-content center
      background-color #141014
      .title
       color #CBAC86
       align-self center
       set-size(18px)
     .content-wrapper
      width 7.5rem
      .item
       width 7.5rem
       font-size 0
       .title
        display flex
        justify-content space-between
        .name
         set-size(14px)
         color #CBAC86
         margin-top 0.44rem
         margin-left 0.31rem
        .time
         set-size(10px)
         color #CBAC86
         margin-top 0.5rem
         margin-right 0.34rem
       .desc
        color #666666
        set-size(12px)
        margin 0.19rem 0.34rem 0 0.31rem
       .images
        width 6.94rem
        margin 0 auto
        .clear
         clear both
        .preview-wrapper
         margin-top 0.12rem
         border solid 0.01rem #CBAC86
         width 2.61rem
         height 2.11rem
         background-size 2.61rem 2.11rem
         img
          width 2.61rem
          height 2.11rem
          background-repeat no-repeat
          background-size 2.61rem 2.11rem
        .image img
         width 1.14rem
         height 0.9rem
         background-size 1.14rem 0.9rem
         background-repeat no-repeat
         margin-right 0.31rem
         margin-top 0.17rem
         float left
        .clearfix:after
         content '.'
         display inline-block
         height 0
         line-height 0
         clear both
       .set-video
        width 6.84rem
        margin 0 auto
        height 3.75rem
        margin-top 0.17rem
        iframe
         width 100%
         height 3.75rem
       .step
        width 7.5rem
        margin-top 0.27rem
        display flex
        flex-direction row-reverse
        margin-bottom 0.23rem
        .icon-del,.icon-che
         display inline-block
         width 0.79rem
         height 0.53rem
         background-size 0.79rem 0.53rem
         background-repeat no-repeat
        .icon-che
         background-image url("btn_xiugai.png")
         margin-right 0.28rem
        .icon-del
         background-image url("btn_shanchu.png")
         margin-right 0.28rem
       .line
        width 6.91rem
        height 0.02rem
        margin 0 auto
        background-image url("line.png")
</style>



