<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div class="title">评价</div>
            </div>
            <div class="content-wrapper">
                <div class="tabs">
                    <div class="tab-item" @click="toreceive">
                        <div :class="[isreceive ? 'select-name' : 'name']">收到的评价</div>
                        <div :class="[isreceive ? 'under-line' : 'select-lin']"></div>
                    </div>
                    <div class="tab-item" @click="tosend">
                        <div :class="[issend ? 'select-name' : 'name']">发出的评价</div>
                        <div :class="[issend ? 'under-line' : 'select-lin']"></div>
                    </div>
                </div>
            </div>
            <div class="line"></div>
            <router-view></router-view>
        </div>
        <navbar selectShow="personal"></navbar>
    </div>
</template>
<script>
    import NavBar from '../navbar/navbar.vue'
    export default {
        data () {
            return {
                isreceive: true,
                issend: false
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
        },
        components: {
            navbar: NavBar
        },
        methods: {
            toreceive () {
                this.$router.replace({path: '/receive-index'})
                this.isreceive = true
                this.issend = false
            },
            tosend () {
                this.$router.replace({path: '/send-index'})
                this.isreceive = false
                this.issend = true
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     position absolute
     bottom 1.03rem
     top 0
     overflow-y scroll
     -webkit-overflow-scrolling touch
     width 7.5rem
     .head
      width 7.5rem
      height 0.89rem
      display flex
      justify-content center
      background-color #141014
      .title
       color #CBAC86
       align-self center
       set-size(18px)
    .content-wrapper
     width 7.5rem
     height 0.81rem
     .tabs
      width 7.5rem
      display flex
      .tab-item
       flex 1
       .name,.select-name
        margin-top 0.24rem
        text-align center
        set-size(18px)
       .name
        color #666666
       .select-name
        color #CBAC86
       .under-line,.select-line
        width 1.61rem
        height 0.05rem
        background-color #CBAC86
        margin 0.18rem auto
       .under-line
        visibility visible
       .select-lin
        visibility hidden
    .line
     width 7.5rem
     height 0.09rem
     background-color #f9f9f9
</style>



