<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div class="title">
                    企业详情
                </div>
            </div>
            <div class="bg-image">
                <img :src="api+result.head_pic">
            </div>
            <div class="data-wrapper">
                <div class="name">
                    <div class="title">企业名称:</div>
                    <div class="value">{{result.company_name}}</div>
                </div>
                <div class="phone">
                    <div class="title">联系方式:</div>
                    <div class="value">{{hidePhone(result.tel)}}</div>
                    <span class="icon" @click="tocustomer"></span>
                </div>
                <div class="service">
                    <div class="title">业务范围:</div>
                    <div class="value">{{result.business}}</div>
                </div>
            </div>
            <div class="line"></div>
            <div class="html-wrapper">
                <div class="title">企业介绍：</div>
                <div class="content-html" v-html="result.introduce"></div>
            </div>
            <div class="line"></div>
            <div class="video-wrapper" v-show="result.company_video !== '' ">
                <div class="title">宣传短片：</div>
                <div class="set-video">
                    <iframe :src="result.company_video"></iframe>
                </div>
            </div>
            <div class="line"></div>
            <div class="comment">
                <div class="tag">用户评价：</div>
                <span v-if="common_show" class="icon" @click="common"></span>
                <span v-else class="w_icon" @click="common"></span>
            </div>
            <div class="line"></div>
            <div class="lists">
                <ul>
                    <li v-for="item in evaluate">
                        <div class="source">
                            <div class="time">{{item.time}}</div>
                            <div class="name">{{item.company_name}}</div>
                        </div>
                        <div class="desc">
                            {{item.content}}
                        </div>
                    </li>
                </ul>
            </div>
            <transition enter-active-class="animated slideInRight"
                        leave-active-class="animated slideOutRight">
                <div class="input-wrapper" v-show="common_show">
                    <div class="text">
                        <el-input
                                v-model="content"
                                type="textarea"
                                :rows="2"
                                placeholder="请输入评价内容" >
                        </el-input>
                    </div>
                    <div class="submit">
                        <span class="btn-cancel" @click="discommon"></span>
                        <span class="btn-ok" @click="sendevaluate"></span>
                        <div class="clearfix"></div>
                    </div>
                </div>
            </transition>
        </div>
        <div class="collect-wrapper">
            <div class="left" @click="getcollect">
                <span v-if="is_collect" class="collect-icon"></span>
                <span v-else class="icon"></span>
                <div class="tag">收藏</div>
            </div>
            <div class="right" @click="showPhone">
                <div class="desc">查看完整号码</div>
            </div>
        </div>
    </div>
</template>
<script>
    import $ from 'jquery'
    import { MessageBox, Toast } from 'mint-ui'
    export default {
        data () {
            return {
                api: '',
                user_id: '',
                business_id: '',
                common_show: false,
                time: '',
                evaluate: [],
                is_collect: false,
                is_phone: false,
                content: '',
                result: {}
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.api = this.$store.state.api
            this.user_id = this.$store.state.user_id
            this.business_id = this.$route.query.business_id
            this.getcompany()
            this.getmydata()
            this.getevaluate()
            this.has_collect()
        },
        components: {},
        methods: {
            tocustomer () {
                if (this.user_id === 0 || typeof (this.user_id) === 'undefined') {
                    this.goLogin()
                } else {
//                我的客户
                    this.$router.push({path: '/confirming-index'})
                }
            },
            getcompany () {
//                企业详情
                this.$http.post(this.api + '/index/user/read', {company_id: this.business_id}).then(function (response) {
                    this.result = response.body
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                })
            },
            goLogin () {
                const obj = this
                MessageBox.confirm('您还没有登录账号,' +
                            '是否去登陆注册 ?').then(function () {
                        obj.$router.push({path: '/login-index'})
                    }, function () {})
            },
            common () {
                if (this.user_id === 0 || typeof (this.user_id) === 'undefined') {
                    this.goLogin()
                } else {
                    if (this.time < 1) {
                        Toast({
                            message: '您的评价次数已经用完了',
                            position: 'bottom',
                            duration: 1000
                        })
                        return
                    }
                    this.common_show = true
                }
            },
            discommon () {
//                取消评价
                this.common_show = false
            },
            getmydata () {
                if (this.user_id === 0 || typeof (this.user_id) === 'undefined') {
                } else {
                    this.$http.post(this.api + '/index/my/myinfo', {
                        user_id: this.user_id
                    }).then(function (response) {
                        this.time = response.body.time
                    }, function (response) {
                        Toast({
                            message: 'error',
                            position: 'bottom',
                            duration: 1000
                        })
                    })
                }
            },
            getevaluate () {
//                评价
                this.$http.post(this.api + '/index/user/evaluate', {
                    company_id: this.business_id
                }).then(function (response) {
                    this.evaluate = response.body.data
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                })
            },
            sendevaluate () {
                if (this.content === '' || typeof (this.content) === 'undefined') {
                    Toast({
                        message: '评价内容不能为空',
                        position: 'bottom',
                        duration: 1000
                    })
                    return
                }
                if (this.time > 0) {
//                    发送评价
                    this.$http.post(this.api + '/index/user/s_evaluate', {
                        company_id: this.business_id,
                        user_id: this.user_id,
                        content: this.content
                    }).then(function (response) {
                        if (response.body.status === 200) {
                            Toast({
                                message: '评价成功',
                                position: 'bottom',
                                duration: 1000
                            })
                            this.time --
                            this.getevaluate()
                            if (this.time < 1) {
                                this.common_show = false
                            } else {
                                this.common_show = true
                            }
                        } else {
                            Toast({
                                message: '评价失败',
                                position: 'bottom',
                                duration: 1000
                            })
                        }
                    }, function (response) {
                        Toast({
                            message: 'error',
                            position: 'bottom',
                            duration: 1000
                        })
                    })
                } else {
                    this.common_show = false
                    Toast({
                        message: '您没有评价的次数了',
                        position: 'bottom',
                        duration: 1000
                    })
                }
            },
            getcollect () {
                if (this.user_id === 0 || typeof (this.user_id) === 'undefined') {
                    this.goLogin()
                } else {
                    if (this.is_collect === true) {
                        this.cancel()
                    } else {
                        this.collect()
                    }
                }
            },
            has_collect () {
                if (this.user_id === 0 || typeof (this.user_id) === 'undefined') {
                    return
                }
//                是否收藏过
                this.$http.post(this.api + '/index/user/check_col', {
                    company_id: this.business_id,
                    user_id: this.user_id
                }).then(function (response) {
                    if (response.body.code === 200) {
                        this.is_collect = true
                    } else {
                        this.is_collect = false
                    }
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                })
            },
            collect () {
//               收藏
                this.$http.post(this.api + '/index/user/collection', {
                    id: this.business_id,
                    user_id: this.user_id
                }).then(function (response) {
                    if (response.body.status === 200) {
                        Toast({
                            message: '收藏成功',
                            position: 'bottom',
                            duration: 1000
                        })
                        this.is_collect = true
                    } else if (response.body.status === 100) {
                        Toast({
                            message: '收藏失败',
                            position: 'bottom',
                            duration: 1000
                        })
                        this.is_collect = false
                    } else if (response.body.status === 300) {
                        Toast({
                            message: '已收藏',
                            position: 'bottom',
                            duration: 1000
                        })
                        this.is_collect = true
                    } else {
                    }
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                })
            },
            cancel () {
//                取消收藏
                this.$http.post(this.api + '/index/my/cancel', {
                    user_id: this.user_id,
                    id: this.business_id
                }).then(function (response) {
                    if (response.body.status === 200) {
                        Toast({
                            message: '取消收藏',
                            position: 'bottom',
                            duration: 1000
                        })
                        this.is_collect = false
                    } else {
                        Toast({
                            message: '取消失败',
                            position: 'bottom',
                            duration: 1000
                        })
                        this.is_collect = true
                    }
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                })
            },
            hidePhone (phone) {
                if (phone === '' || typeof (phone) === 'undefined') {
                    return
                }
                if (this.is_phone === false) {
                    var lphone = phone.replace(/^(\d{4})\d{4}(\d+)/, '$1****$2')
                } else {
                     lphone = phone
                }
                return lphone
            },
            showPhone () {
                if (this.user_id === 0 || typeof (this.user_id) === 'undefined') {
                    this.goLogin()
                } else {
                    this.is_phone = true
                    $('.wrapper').scrollTop(0, 0)
                }
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .collect-wrapper
     width 7.5rem
     height 1.03rem
     position  fixed
     left 0
     bottom 0
     display flex
     .left
      flex 0 0 2.99rem
      font-size 0
      display flex
      justify-content center
      .icon,.collect-icon
       width 0.47rem
       height 0.47rem
       background-size 0.47rem 0.47rem
       background-repeat no-repeat
       display inline-block
       align-self center
      .collect-icon
       background-image url("icon_shoucang_dianji.png")
      .icon
       background-image url("icon_shoucang-.png")
      .tag
       color #333333
       set-size(18px)
       font-weight 500
       display inline-block
       align-self center
       margin-left 0.14rem
     .right
      flex 1
      background-color #DAC098
      display flex
      justify-content center
      .desc
       color #ffffff
       set-size(18px)
       display inline-block
       align-self center
    .wrapper
     position absolute
     bottom 0.99rem
     top 0
     overflow-y scroll
     -webkit-overflow-scrolling touch
     width 7.5rem
     .head
      width 7.5rem
      height 0.89rem
      display flex
      background-color #141014
      justify-content center
      .title
       set-size(18px)
       color #CBAC86
       align-self center
     .bg-image
      img
       width 7.5rem
       height 3rem
       background-size 7.5rem 3rem
       background-repeat no-repeat
     .data-wrapper
      width 7.5rem
      margin-bottom 0.32rem
      font-size 0
      .name,.phone,.service
       display inline-block
       margin-left 0.3rem
      .name,.service
       margin-top 0.41rem
       display flex
      .phone
        margin-top 0.34rem
        display flex
        justify-items  center
       .icon
         display inline-block
         width 1.61rem
         height 0.51rem
         background-repeat no-repeat
         background-size 1.61rem 0.51rem
         background-image url("btn_hetongfuwu.png")
         margin-left 0.95rem
       .title
        align-self center
        color #333333
        set-size(15px)
        display inline-block
        font-weight 600
       .value
        align-self center
        color #666666
        set-size(14px)
        display inline-block
        margin-left 0.19rem
        max-width 5rem
        overflow hidden
        text-overflow ellipsis
     .line
      width 6.9rem
      height 0.02rem
      background-color #E8E8E8
      margin auto 0.3rem auto 0.3rem
     .html-wrapper
      width 7.5rem
      .title
       color #333333
       set-size(15px)
       font-weight 600
       margin-top 0.33rem
       margin-left 0.3rem
      .content-html
       width 6.87rem
       max-width 6.87rem
       margin 0.14rem auto 0.42rem auto
       color #666666
       set-size(14px)
      .content-html *
        max-width 6.87rem !important
        width 6.87rem !important
     .video-wrapper
      width 7.5rem
      .title
       color #333333
       set-size(15px)
       font-weight 600
       margin-left 0.3rem
       margin-top 0.39rem
      .set-video
       margin 0.16rem 0.3rem 0.44rem 0.3rem
       iframe
        width 100%
        height 3.75rem
     .comment
      width 7.5rem
      display flex
      justify-content space-between
      .tag
       color #333333
       set-size(15px)
       display inline-block
       margin 0.27rem 0 0.33rem 0.29rem
       font-weight 600
      .icon,.w_icon
       width 1.41rem
       height 0.51rem
       display inline-block
       background-size 1.14rem 0.51rem
       background-repeat no-repeat
       margin-right 0.29rem
       margin-top 0.19rem
      .w_icon
       background-image url("btn_woyaopingjia02.png")
      .icon
       background-image url("btn_woyaopingjia.png")
     .lists
      width 7.5rem
      margin-bottom 0.5rem
      .source
       font-size 0
       margin-top 0.29rem
       margin-right 0.44rem
       margin-left 0.29rem
       .time
        display inline-block
        color: #666666
        set-size(12px)
       .name
        display inline-block
        color #666666
        set-size(12px)
        margin-left 0.35rem
      .desc
       color #999999
       set-size(14px)
       margin-top 0.2rem
       margin-right 0.44rem
       margin-left 0.29rem
     .input-wrapper
      width 7.5rem
      .text
       height 1.61rem
       margin-left 0.29rem
       margin-right 0.3rem
      .submit
       width 7.5rem
       margin-top 0.51rem
       margin-right 0.3rem
       .btn-ok, .btn-cancel
        width 1.41rem
        height 0.51rem
        background-size 1.41rem 0.51rem
        background-repeat no-repeat
        display inline-block
        float right
       .btn-ok
        background-image url("btn_queding.png")
       .btn-cancel
        background-image url("btn_quxiao.png")
        margin-left 0.29rem
       .clearfix:after
        content '.'
        display inline-block
        height 0
        line-height 0
        clear both
        visibility hidden
</style>



