<template>
    <div class="main">
        <div class="wrapper">
            <div class="head">
            </div>
            <div class="logo">
                <span></span>
            </div>
            <div class="input-wrapper">
                <div class="phone-wrapper">
                    <div class="phone">
                        <span class="icon-phone"></span>
                        <input type="tel" class="input-phone" v-model="tel" placeholder="输入您的手机号">
                    </div>
                    <div class="code">
                        <span v-if="wait === 60" class="icon-code" @click="sendCode"></span>
                        <span v-else class="icon-wait">{{wait}}秒</span>
                    </div>
                    <div class="t-line"></div>
                </div>
                <div class="pass-wrapper">
                    <div class="pass">
                        <span class="icon-pass"></span>
                        <input type="number" class="input-pass" placeholder="输入验证码" v-model="code">
                    </div>
                    <div class="t-line"></div>
                </div>
                <div class="btn-wrapper">
                    <span v-if="this.type === '0'" class="icon-btn" @click="register"></span>
                    <span v-else-if="this.type === '1'" class="icon-code" @click="register"></span>
                    <span v-else class="icon-btn" @click="register"></span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import { Toast } from 'mint-ui'
    export default {
        data () {
            return {
                api: '',
                tel: '',
                wait: 60,
                code: '',
                send_code: '',
                type: '',
                title: '注册'
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.api = this.$store.state.api
            this.type = this.$route.params.type
            if (this.type === '0') {
                this.title = '注册'
            } else if (this.type === '1') {
                this.title = '忘记密码'
            } else {
                this.title = '注册'
            }
            document.title = this.title
        },
        components: {},
        methods: {
            register () {
                    if (this.tel === '' || typeof (this.tel) === 'undefined') {
                        Toast({
                            message: '请输入手机号',
                            position: 'bottom',
                            duration: 1000
                        })
                        return
                    }
                    if (!(/^1[3|4|5|7|8]\d{9}$/.test(this.tel))) {
                        Toast({
                            message: '请检查手机格式是否正确',
                            position: 'bottom',
                            duration: 1000
                        })
                        return
                    }
                    if (this.code === '' || typeof (this.code) === 'undefined') {
                        Toast({
                            message: '请输入验证码',
                            position: 'bottom',
                            duration: 1000
                        })
                        return
                    }
                    if (this.code === this.send_code + '') {
                        if (this.type === '0') {
                            this.$router.push({name: 'PassIndex', params: {phone: this.tel, type: '0'}})
                        } else {
                            this.$router.push({name: 'PassIndex', params: {phone: this.tel, type: '1'}})
                        }
                    } else {
                        Toast({
                            message: '验证码匹配失败',
                            position: 'bottom',
                            duration: 1000
                        })
                    }
            },
            sendCode () {
                if (this.wait === 60) {
                    if (this.tel === '' || typeof (this.tel) === 'undefined') {
                        Toast({
                            message: '请输入手机号',
                            position: 'bottom',
                            duration: 1000
                        })
                        return
                    }
                    if (!(/^1[3|4|5|7|8]\d{9}$/.test(this.tel))) {
                        Toast({
                            message: '请检查手机格式是否正确',
                            position: 'bottom',
                            duration: 1000
                        })
                        return
                    }
                    this.time()
                    this.$http.post(this.api + '/index/register/register', {tel: this.tel}).then(function (response) {
                        if (response.status === 200) {
                            let data = response.body
                            this.send_code = data.code
                            Toast({
                                message: '验证码已发送',
                                position: 'bottom',
                                duration: 1000
                            })
                        }
                    }, function (response) {
                        Toast({
                            message: 'error',
                            position: 'bottom',
                            duration: 1000
                        })
                    })
                }
            },
            time () {
                const obj = this
                if (this.wait === 0) {
                    this.wait = 60
                } else {
                    this.wait --
                    setTimeout(function () {
                        obj.time()
                    }, 1000)
                }
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../../common/stylus/base.styl"
   .main
    background-color #141014   
    height 100%   
    .wrapper
     width 7.5rem
     background-color #141014
     height 100%   
     .head
      width 7.5rem
      height 0.89rem
      display flex
      justify-content center
      .title
       color #CBAC86
       align-self center
       set-size(18px)
     .z-line
      width 7.5rem
      height 0.02rem
      background-color #CBAC86
     .logo
      width 7.5rem
      span
       display block
       width 3.91rem
       height 0.83rem
       background-size 3.91rem 0.83rem
       background-repeat no-repeat
       margin 1.54rem auto 0 auto
       background-image url("pic_logo.png")
     .input-wrapper
      width 7.5rem
      background-color #141014
      .phone-wrapper
       width 7.5rem
       position relative
       margin-top 1.64rem
       background-color #141014
       .phone
        width 7.5rem
        display flex
        justify-content center
        font-size 0
        padding-bottom 0.17rem
        background-color #141014
        .icon-phone
         width 0.29rem
         height 0.44rem
         background-image url("icon_phone.png")
         background-size 0.29rem 0.44rem
         background-repeat no-repeat
         align-self center
        .input-phone
         border none
         color #E4CDA4
         set-size(16px)
         background-color #141014
         margin-left 0.34rem
         align-self center
         padding-bottom 0
       .code
        .icon-code,.icon-wait
         width 1.61rem
         height 0.53rem
         background-repeat no-repeat
         background-size 1.61rem 0.53rem
         display inline-block
         position absolute
         bottom 0.18rem
         right 1.04rem
        .icon-code
         background-image url("btn_fasongyanzhengma.png")
        .icon-wait
         color #FFFFFF
         set-size(16px)
         line-height 0.53rem
         text-align center
       .t-line
        width 5.41rem
        height 0.02rem
        margin 0 auto
        background-color #CBAC86
      .pass-wrapper
       width 7.5rem
       padding-top 0.41rem
       background-color #141014
       .pass
        width 7.5rem
        display flex
        justify-content center
        font-size 0
        padding-bottom 0.17rem
        background-color #141014
        .icon-pass
         width 0.38rem
         height 0.44rem
         background-image url("icon_mima.png")
         background-size 0.38rem 0.44rem
         background-repeat no-repeat
         align-self center
        .input-pass
         border none
         color #E4CDA4
         set-size(16px)
         background-color #141014
         margin-left 0.34rem
         align-self center
         padding-bottom 0
       .t-line
        width 5.41rem
        height 0.02rem
        margin 0 auto
        background-color #CBAC86
     .btn-wrapper
      width 7.5rem
      padding-top 1.41rem
      text-align center
      background-color #141014
      .icon-btn,.icon-code
       width 5.41rem
       height 0.81rem
       background-size 5.41rem 0.81rem
       background-repeat no-repeat
       display inline-block
       background-color #141014
      .icon-code
       background-image url("btn_querenxiugai.png")
      .icon-btn
       background-image url("btn_quedingzhuce.png")
</style>



