<template>
    <div>
        <div class="wrapper">
            <div class="line"></div>
            <div class="content">
                <ul>
                    <li class="item" v-for="(item, index) in result">
                        <div class="title">
                            <div class="name">
                                {{item.company_name}}
                            </div>
                            <div class="time">
                                {{item.create_time}}
                            </div>
                        </div>
                        <div class="desc">
                            <textarea :class="disable[index] ? 'inputType' : 'inputUpdate'" type="text"
                                      v-model="item.evaluate_content" :disabled="disable[index]">

                            </textarea>
                        </div>
                        <div class="step" v-if="disable[index]">
                            <span class="icon-del" @click="del(item.id)"></span>
                            <span class="icon-check" @click="show(index)"></span>
                        </div>
                        <div v-else class="step-update">
                            <span class="update-cancel" @click="hide"></span>
                            <span class="update-ok" @click="update(item.id, item.evaluate_content)"></span>
                        </div>
                        <div class="line-new"></div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
    import {Toast, Indicator} from 'mint-ui'
    export default {
        data () {
            return {
                user_id: '',
                api: '',
                result: [],
                disable: []
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.user_id = this.$store.state.user_id
            this.api = this.$store.state.api
            Indicator.open()
            this.getsend()
        },
        components: {},
        methods: {
            getsend () {
                this.$http.post(this.api + '/index/my/post', {user_id: this.user_id}).then(function (response) {
                    this.result = response.body
                    for (var i = 0; i < this.result.length; i++) {
                        this.$set(this.disable, i, true)
                    }
                    Indicator.close()
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 3000
                    })
                    Indicator.close()
                })
            },
            del (id) {
//                删除评价
                this.$http.post(this.api + '/index/my/del_eva', {id: id}).then(function (response) {
                    if (response.body.status === 200) {
                        Toast({
                            message: '删除成功',
                            position: 'bottom',
                            duration: 3000
                        })
                        this.getsend()
                    } else {
                        Toast({
                            message: '删除失败',
                            position: 'bottom',
                            duration: 3000
                        })
                    }
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 3000
                    })
                })
            },
            update (id, content) {
//                修改发出的评价
                this.$http.post(this.api + '/index/my/update_eva', {
                    id: id,
                    content: content
                }).then(function (response) {
                    if (response.body.status === 200) {
                        Toast({
                            message: '修改成功',
                            position: 'bottom',
                            duration: 3000
                        })
                        this.getsend()
                    } else {
                        Toast({
                            message: '修改失败',
                            position: 'bottom',
                            duration: 3000
                        })
                    }
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 3000
                    })
                })
            },
            show (index) {
                for (var i = 0; i < this.disable.length; i++) {
                    if (i === index) {
                        this.$set(this.disable, i, false)
                    } else {
                        this.$set(this.disable, i, true)
                    }
                }
            },
            hide () {
                this.getsend()
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../../common/stylus/base.styl"
    .wrapper
     width 7.5rem
     .line
      width 7.5rem
      background-color #f9f9f9
      height 0.05rem
     .content
      width 7.5rem
      margin-top 0.18rem
      .item
       width 7.5rem
       font-size 0
       .title
        display flex
        margin-top 0.3rem
        justify-content space-between
        .name
         set-size(14px)
         color #CBAC86
         margin-left 0.3rem
        .time
         color #CBAC86
         set-size(10px)
         margin-right 0.35rem
       .desc
        color #666666
        set-size(13px)
        margin 0.14rem 0.44rem 0 0.3rem
        .inputType,.inputUpdate
        textarea
         set-size(13px)
         width 100%
         border none
        .inputUpdate
        textarea
         background-color #FBFBFB
         color #999999
        .inputType
        textarea
         color #666666
         background-color #ffffff
       .line-new
        width 6.91rem
        height 0.02rem
        background-image url("line.png")
        margin 0.22rem auto
       .step
        width 7.5rem
        display flex
        flex-direction row-reverse
        margin-top 0.21rem
        .icon-del,.icon-check
         display inline-block
         width 0.79rem
         height 0.53rem
         background-repeat no-repeat
         background-size 0.79rem 0.53rem
        .icon-del
         background-image url("btn_shanchu.png")
         margin-left 0.35rem
         margin-right 0.3rem
        .icon-check
         background-image url("btn_xiugai.png")
       .step-update
        width 7.5rem
        display flex
        flex-direction row-reverse
        margin-top 0.21rem
        .update-cancel,.update-ok
         display inline-block
         width 1.34rem
         height 0.52rem
         background-repeat no-repeat
         background-size 1.34rem 0.52rem
        .update-ok
         background-image url("btn_querenxiugai.png")
         margin-left 0.45rem
         margin-right 0.3rem
        .update-cancel
         background-image url("btn_quxiaoxiugai.png")
         margin-right 0.3rem
</style>



