<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div></div>
                <div class="title">{{title}}</div>
                <span v-if="!isShow" class="icon" @click="show"></span>
                <span v-else class="icon-h" @click="hide"></span>
            </div>
            <div class="content-wrapper">
                <div class="tabs">
                    <div class="tab-item" @click="tobusinesses">
                        <div :class="[busactive ? 'select-name' : 'name']">企业列表</div>
                        <div :class="[busactive ? 'under-line' : 'select-lin']"></div>
                    </div>
                    <div class="tab-item" @click="tonotice">
                        <div :class="[noactive ? 'select-name' : 'name']">公告栏</div>
                        <div :class="[noactive ? 'under-line' : 'select-lin']"></div>
                    </div>
                </div>
            </div>
            <div class="line"></div>
            <transition enter-active-class="animated slideInRight"
                        leave-active-class="animated slideOutRight">
                <div class="type-wrapper" v-if="isShow">
                    <ul>
                        <li class="type" v-for="(item, index) in result"
                            @click="update(item.category_id, index)">
                            {{item.c_name}}
                        </li>
                    </ul>
                </div>
            </transition>
            <router-view></router-view>
        </div>
        <navbar selectShow="company"></navbar>
    </div>
</template>
<script>
    import {Toast} from 'mint-ui'
    import NavBar from '../navbar/navbar.vue'
    export default {
        data () {
            return {
                api: '',
                isShow: false,
                busactive: true,
                noactive: false,
                title: '',
                result: [],
                id: '',
                id2: ''
            }
        },
        watch: {
            '$route': function (val, oldVal) {
                if (this.$route.path === '/machine-index') {
                    document.documentElement.style.fontSize =
                            document.documentElement.clientWidth / 7.5 + 'px'
                    this.api = this.$store.state.api
                    this.title = this.$store.state.value
                    this.id = this.$store.state.id
                    this.id2 = this.$store.state.id2
                    this.tobusinesses()
                    this.getcassify()
                }
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.api = this.$store.state.api
            this.title = this.$store.state.value
            this.id = this.$store.state.id
            this.id2 = this.$store.state.id2
            this.tobusinesses()
            this.getcassify()
        },
        components: {
            navbar: NavBar
        },
        methods: {
            tobusinesses () {
                this.$router.replace({name: 'BusinessesIndex'})
                this.busactive = true
                this.noactive = false
            },
            tonotice () {
                this.$router.replace({path: '/notice-index'})
                this.busactive = false
                this.noactive = true
            },
            hide () {
                this.isShow = false
            },
            show () {
                this.isShow = true
            },
            getcassify () {
                this.$http.get(this.api + '/index/user/category').then(function (response) {
                    this.result = response.body.data
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 3000
                    })
                })
            },
            update (id, index) {
                if (id === 1) {
//                    机械类
                    this.$router.replace({name: 'MachineIndex'})
                } else {
//                    非机械类
                    this.$router.replace({name: 'EngineerIndex'})
                }
                this.$store.state.id = id
                this.$store.state.id2 = this.result[index].child[0].category_id
                this.$store.state.value = this.result[index].child[0].c_name
                this.hide()
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     position absolute
     bottom 1.03rem
     top 0
     width 7.5rem
     background-color #ffffff
     .head
      width 7.5rem
      height 0.89rem
      background-color #141014
      display flex
      justify-content space-between
      position fixed
      right 0
      top 0
      bottom 0
      z-index 100
      .title
       align-self center
       set-size(18px)
       color #CBAC86
      .icon-h
       background-image url("icon_quxiao-.png")
      .icon
       background-image url("icon_Hamburger.png")
      .icon,.icon-h
       background-repeat no-repeat
       display inline-block
       align-self center
       margin-right 0.3rem
       width 0.26rem
       height 0.26rem
       background-size 0.26rem 0.26rem
     .content-wrapper
      width 7.5rem
      height 0.81rem
      position fixed
      margin-top 0.89rem
      .tabs
       width 7.5rem
       display flex
       background-color #ffffff
       .tab-item
        flex 1
        .name,.select-name
         margin-top 0.24rem
         text-align center
         set-size(18px)
        .name
         color #666666
        .select-name
         color #CBAC86
        .under-line,.select-line
         width 1.61rem
         height 0.05rem
         background-color #CBAC86
         margin 0.18rem auto
        .under-line
         visibility visible
        .select-lin
         visibility hidden
     .line
      width 7.5rem
      height 0.09rem
      background-color #f9f9f9
     .type-wrapper
      position fixed
      right 0
      top 0
      bottom 0
      z-index 101
      background-color #212121
      overflow auto
      height auto
      width 2.71rem
      margin-top 0.89rem
      margin-bottom 0.99rem
      .type
       set-size(15px)
       color #ffffff
       padding 0.27rem 0.61rem 0.26rem 0.62rem
       font-weight 600
       border-bottom 0.02rem solid #999999

</style>



