<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div class="title">企业资料</div>
            </div>
            <div class="item">
                <div class="tag">用户名称</div>
                <div class="value">{{data.user_name}}</div>
            </div>
            <div class="item">
                <div class="tag">公司名称</div>
                <div class="value">{{data.company_name}}</div>
            </div>
            <div class="item">
                <div class="tag">企业地址</div>
                <div class="value">{{data.company_address}}</div>
            </div>
            <div class="item">
                <div class="tag">联系方式</div>
                <div class="value" @click="telphone(data.tel)">{{data.tel}}</div>
            </div>
            <div class="item">
                <div class="tag">商务QQ</div>
                <div class="value">{{data.qq}}</div>
            </div>
            <div class="item">
                <div class="tag">业务范围</div>
                <div class="value">{{data.category}}</div>
            </div>
            <div class="item">
                <div class="tag">企业简介</div>
                <div class="value" v-html="data.introduce"></div>
            </div>
        </div>
    </div>
</template>
<script>
    import { Toast } from 'mint-ui'
    export default {
        data () {
            return {
                api: '',
                data: {},
                user_id: ''
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.api = this.$store.state.api
            this.user_id = this.$store.state.user_id
            this.getmydata()
        },
        components: {},
        methods: {
            telphone (phone) {
                window.location.href = 'tel:' + phone
            },
            getmydata () {
                this.$http.post(this.api + '/index/my/myinfo', {
                    user_id: this.user_id
                }).then(function (response) {
                    this.data = response.body
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                })
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     position absolute
     bottom 0rem
     top 0
     overflow-y scroll
     -webkit-overflow-scrolling touch
     width 7.5rem
     .head
      width 7.5rem
      height 0.89rem
      display flex
      justify-content center
      background-color #141014
      .title
       color #CBAC86
       align-self center
       set-size(18px)
     .item
      border-bottom inset 1px #CFCFCF
      width 7.49rem
      margin 0 auto
      .tag
       set-size(16px)
       line-height 0.36rem
       margin-top 0.38rem
       margin-left 0.3rem
       color #D3B894
      .value
       set-size(13px)
       color #999999
       margin 0.18rem 0.34rem 0.38rem 0.31rem

</style>



