<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div class="title">新闻详情</div>
            </div>
            <div class="bg-image">
                <img :src="api+result.img">
            </div>
            <div class="title-wrapper">
                <div class="name">{{result.title}}</div>
                <div class="note">
                    <div class="date">
                        发布日期：{{result.time}}
                    </div>
                    <div class="desc">来源：{{result.from}} </div>
                </div>
            </div>
            <div class="line"></div>
            <div class="content-html" v-html="result.content">
            </div>
            <div class="bg-line"></div>
            <div class="news-wrapper">
                <div class="title">
                    <div class="zh-sign">行业新闻</div>
                    <div class="en-sign">INDUSTRY NEWS</div>
                </div>
                <div class="news">
                    <ul>
                        <li class="content-wrapper" @click="tonews(item.id)" v-for="item in newsContent">
                            <div class="icon">
                                <img :src="api+item.img">
                            </div>
                            <div class="desc">{{item.title}}</div>
                            <div class="date">
                                <div class="tag">发布日期</div>
                                <div class="time">{{item.time}}</div>
                            </div>
                            <div class="clearfix"></div>
                        </li>
                    </ul>
                </div>
         </div>
        </div>
        <navbar selectShow="home"></navbar>
    </div>
</template>
<script>
    import {Toast, Indicator} from 'mint-ui'
    import Navbar from '../navbar/navbar.vue'
    import $ from 'jquery'
    export default {
        data () {
            return {
                api: '',
                id: '',
                result: {},
                newsContent: []
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.api = this.$store.state.api
            this.id = this.$route.params.news_id
            Indicator.open()
            this.getnews()
            this.news()
        },
        components: {
            navbar: Navbar
        },
        methods: {
            getnews () {
                this.$http.post(this.api + '/index/index/read', {
                    id: this.id
                }).then(function (response) {
                    this.result = response.body
                    $('.wrapper').scrollTop(0, 0)
                    Indicator.close()
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 3000
                    })
                    Indicator.close()
                })
            },
            news () {
//                新闻
                this.$http.get(this.api + '/index/index/news').then(function (response) {
                    if (response.status === 200) {
                        this.newsContent = response.body
                    }
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                })
            },
            tonews (id) {
//                新闻详情
                this.id = id
                Indicator.open()
                this.getnews()
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     position absolute
     bottom 1.03rem
     top 0
     overflow-y scroll
     -webkit-overflow-scrolling touch
     width 7.5rem
     .head
      width 7.5rem
      height 0.89rem
      display flex
      justify-content center
      background-color #141014
      .title
       color #CBAC86
       align-self center
       set-size(18px)
     .bg-image
      img
       width 7.5rem
       height 3rem
       background-size 7.5rem 3rem
       background-repeat no-repeat
     .news-wrapper
      width 7.5rem
      .title
       font-size 0
       margin-left 0.28rem
       margin-top 0.48rem
       .zh-sign
        color #333333
        set-size(18px)
        font-weight 600
       .en-sign
        color #CBAC86
        set-size(12px)
        margin-top 0.08rem
      .news
       width 7.5rem
       .content-wrapper
        font-size 0
        float left
        margin-left 0.29rem
        .icon
         img
          width 3.3rem
          height 2.38rem
          margin-top 0.34rem
        .clearfix:after
         content '.'
         display inline-block
         height 0
         line-height 0
         clear both
         visibility hidden
        .desc
         margin-top 0.08rem
         color #666666
         width 3.08rem
         set-size(12px)
         white-space nowrap
         overflow hidden
         text-overflow ellipsis
        .date
         margin-top 0.16rem
         .tag
          color #999999
          set-size(10px)
          display inline-block
         .time
          color #999999
          set-size(10px)
          display inline-block
          margin-left 0.1rem
     .title-wrapper
      width 7.5rem
      font-size 0
      .name
       set-size(15px)
       color #333333
       white-space nowrap
       text-overflow ellipsis
       overflow hidden
       margin 0.25rem 0.61rem 0 0.32rem
      .note
       width 7.5rem
       display flex
       margin-top 0.18rem
       .date
        set-size(12px)
        color #999999
        margin-left 0.29rem
        white-space nowrap
        text-overflow ellipsis
        overflow-y hidden
       .desc
        margin-left 0.42rem
        margin-right 1.27rem
        white-space nowrap
        overflow-y hidden
        text-overflow ellipsis
        set-size(12px)
        color #999999
     .line
      width 6.91rem
      height 0.02rem
      background-color #E8E8E8
      margin 0.31rem auto
     .content-html
      width 6.87rem
      margin 0.29rem 0.32rem 0.62rem 0.31rem
      set-size(14px)
      color #666666
      line-height 18px
      *
       max-width 100%
     .bg-line
      width 7.5rem
      height 0.1rem
      background-color #f9f9f9
</style>
<style>
    .content-html *{
        width: 100%;
        max-width: 100%;
    }
</style>



