<template>
    <div class="main">
        <div class="wrapper">
            <div class="head">
                <div class="title"></div>
            </div>
            <div class="logo">
                <span></span>
            </div>

            <div class="phone-wrapper">
                <div class="content">
                    <span class="icon-phone"></span>
                    <input type="tel" class="input-phone" v-model="tel" placeholder="请输入您的手机号">
                </div>
                <div class="t-line"></div>
            </div>
            <div class="pass-wrapper">
                <div class="content">
                    <div class="left">
                        <span class="icon-pass"></span>
                        <input v-if="view_type === 'password'" type="password" class="input-pass" v-model="password" placeholder="输入您的密码">
                        <input v-else type="text" class="input-pass" v-model="password" placeholder="输入您的密码">
                    </div>
                    <div class="right">
                        <span class="icon-yan" @click="yanClick"></span>
                    </div>
                </div>
                <div class="t-line"></div>
            </div>
            <div class="login-wrapper">
                <span class="icon-login" @click="login"></span>
            </div>
            <div class="register-wrapper">
                <span class="icon" @click="toregister"></span>
            </div>
            <div class="forget" @click="toforget">忘记密码</div>
        </div>
    </div>
</template>
<script>
    import { Toast } from 'mint-ui'
    export default {
        data () {
            return {
                api: '',
                tel: '',
                password: '',
                view_type: 'password'
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.api = this.$store.state.api
            document.title = '登录'
        },
        components: {},
        methods: {
            toregister () {
//                注册
                this.$router.push({name: 'RegisterIndex', params: {type: '0'}})
            },
            toforget () {
                this.$router.push({name: 'RegisterIndex', params: {type: '1'}})
            },
            tohome () {
                this.$router.replace({path: '/home-index'})
            },
            gettoken () {
                this.$http.get().then(function (response) {
                }, function (response) {})
            },
            getuserinfo () {
                this.$http.get().then(function (response) {
                }, function (response) {})
            },
            yanClick () {
                if (this.view_type === 'password') {
                    this.view_type = 'text'
                } else {
                    this.view_type = 'password'
                }
            },
            login () {
//                登陆
                if (this.tel === '' || typeof (this.tel) === 'undefined') {
                    Toast({
                        message: '请输入手机号',
                        position: 'bottom',
                        duration: 1000
                    })
                    return
                }
                if (!(/^1[3|4|5|7|8]\d{9}$/.test(this.tel))) {
                    Toast({
                        message: '请检查手机格式是否正确',
                        position: 'bottom',
                        duration: 1000
                    })
                    return
                }
                if (this.password === '' ||
                        typeof (this.password) === 'undefined') {
                    Toast({
                        message: '请输入密码',
                        position: 'bottom',
                        duration: 1000
                    })
                    return
                }
                if (this.password.indexOf(' ') !== -1) {
                    Toast({
                        message: '密码含有非法字符串',
                        position: 'bottom',
                        duration: 1000
                    })
                    return
                }
                if (this.password.length < 5 || this.password.length > 20) {
                    Toast({
                        message: '密码长度至少大于5小于20',
                        position: 'bottom',
                        duration: 1000
                    })
                    return
                }
                this.$http.post(this.api + '/index/login/login', {
                    tel: this.tel, password: this.password}
                ).then(function (response) {
                    if (response.body.status === 200) {
                        Toast({
                            message: response.body.msg,
                            position: 'bottom',
                            duration: 1000
                        })
                        var userId = response.body.data.user_id
                        this.$store.commit('setUserid', userId)
                        this.$router.replace({path: '/home-index'})
                    } else {
                        Toast({
                            message: response.body.msg,
                            position: 'bottom',
                            duration: 1000
                        })
                    }
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 1000
                    })
                })
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../../common/stylus/base.styl"
   .main
    height 100%
    .wrapper
     width 7.5rem
     background-color #141014
     height 100%
     .head
      width 7.5rem
      height 0.89rem
      display flex
      justify-content center
      .title
       color #CBAC86
       align-self center
       set-size(18px)
     .line
      width 7.5rem
      height 0.02rem
      background-color #CBAC86
     .logo
      width 7.5rem
      span
       display block
       width 3.91rem
       height 0.83rem
       background-size 3.91rem 0.83rem
       background-repeat no-repeat
       background-image url("pic_logo.png")
       margin 1.54rem auto 0 auto
     .phone-wrapper
      margin-top 1.64rem
      width 7.5rem
      background-color #141014
      .content
       width 100%
       display flex
       justify-content center
       font-size 0
       padding-bottom 0.18rem
       .icon-phone
        width 0.29rem
        height 0.44rem
        background-size 0.29rem 0.44rem
        background-repeat no-repeat
        background-image url("icon_phone.png")
        align-self center
       .input-phone
        border none
        color #E4CDA4
        set-size(16px)
        background-color #141014
        margin-left 0.34rem
        align-self center
        padding-bottom 0
      .t-line
       margin 0 auto
       width 5.3rem
       height 0.02rem
       background-color #CBAC86
     .pass-wrapper
      width 7.5rem
      padding-top 0.41rem
      background-color #141014
      .content
       width 100%
       display flex
       justify-content center
       font-size 0
       position relative
       padding-bottom 0.18rem
       .left
        display flex
        justify-content center
        .icon-pass
         width 0.38rem
         height 0.44rem
         background-image url("icon_mima.png")
         background-size 0.38rem 0.44rem
         background-repeat no-repeat
         align-self center
        .input-pass
         border none
         color #E4CDA4
         set-size(16px)
         background-color #141014
         margin-left 0.3rem
         align-self center
         padding-bottom 0
       .right
        .icon-yan
         width 0.4rem
         height 0.29rem
         background-size 0.4rem 0.29rem
         background-repeat no-repeat
         background-image url("pic_yan.png")
         display inline-block
         position absolute
         top 0.1rem
         right 1.33rem
      .t-line
       margin 0 auto
       width 5.3rem
       height 0.02rem
       background-color #CBAC86
     .login-wrapper
      width 7.5rem
      display flex
      justify-content center
      font-size 0
      padding-top 1.04rem
      background-color #141014
      .icon-login
       width 5.4rem
       height 0.8rem
       background-size 5.4rem 0.8rem
       background-repeat no-repeat
       background-image url("btn_denglu.png")
     .register-wrapper
      width 7.5rem
      display flex
      justify-content center
      padding-top 0.5rem
      background-color #141014
      .icon
       width 5.4rem
       height 0.8rem
       background-repeat no-repeat
       background-size 5.4rem 0.8rem
       background-image url("btn_zhuce.png")
     .forget
      color #CBAC86
      set-size(15px)
      width 7.5rem
      text-align center
      padding-top 2.1rem
      padding-bottom 0.83rem
      background-color #141014
</style>



